<template>
  <div class="home">
    <HomeTitle>编辑推荐</HomeTitle>
    <ul class="home-cardlist">
      <!-- <CardListItem
        v-for="item in personalizeds"
        :key="item.id"
        @click.native="$router.push('/playlist?id=' + item.id)"
      >
        {{ item.name }}
      </CardListItem> -->

      <!-- @done 在父组件中定义一下这个item :item="item"-->
      <CardListItem
        v-for="item in personalizeds"
        :key="item.id"
        :item="item"
      />

    </ul>

    <HomeTitle>最新音乐</HomeTitle>
    <ul class="home-songlist">
      <!-- 从子组件传出的默认值，在父组件里通过$event这个变量来接收的。$event并不一定是事件对象，他是子组件传给父组件的参数。 -->
      <SongListItem
        v-show="isLoading == false"
        v-for="(item,index) in newsongs"
        :key="index+item.id"
        :item="item"
        :currentSongId="currentSongId"
        :playing="playing"
        @change-current-song="$emit('change-current-song',$event),
        $emit('change-current-play-list',newsongs)"
      >
      </SongListItem>
      <div
        class="loading"
        v-show="isLoading == true"
      ><img
          src="../assets/loading.gif"
          alt="loading"
        >
        <p>加载中...</p>
      </div>
    </ul>
    <!-- @bug 在home主页添加页尾时，出现页尾距离页面底部大约有20px的距离 -->
    <div
      class="home-foot"
      :class="{ 'pad-bottom':currentSong}"
      v-show="isLoading == false"
    >
      <div class="f-wrap">

        <div class="logo"></div>
        <div class="open-app">打开APP，发现更多好音乐 ></div>
        <p class="copyright">网易公司版权所有©1997-2021 杭州乐读科技有限公司运营</p>
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import HomeTitle from '@/components/HomeTitle.vue'
import CardListItem from '@/components/CardListItem.vue'
import SongListItem from '@/components/SongListItem.vue'



export default {

  name: "Home",
  components: {
    HomeTitle,
    CardListItem,
    SongListItem
  },
  props: {
    currentSongId: {
      type: Number,
    },
    playing: Boolean,
    currentSong: Object,
  },
  data: function () {
    return {
      personalizeds: [],
      newsongs: [],
      isLoading: true,
    };
  },
  created() {
    // 发起ajax请求，是因为这是最早可以访问this
    this.axios
      .get("/personalized?limit=6")
      .then((res) => {
        // console.log(this, res);
        this.personalizeds = res.data.result;
        // this.loaded();
      });
    this.axios
      .get("/personalized/newsong")
      .then((res) => {
        this.newsongs = res.data.result;
        this.loaded();
      });
  },
  methods: {
    loaded() {
      setTimeout(() => {
        this.isLoading = false;
      }, 200)
    }
  }
};
</script>
