<template>
  <!-- @todo 判断item里面的song存不存在，存在用song -->
    <li
      v-if="item.song"
      class="songlistitem"
      @click.stop="$emit('change-current-song',item), $emit('add-history-play-list')"
    >
      <div
        class="icon-state"
        v-if="currentSongId === item.id"
      >
        <div
          class="play"
          :class="{current: currentSongId === item.id, playing: playing}"
        >
          <i></i>
          <i></i>
          <i></i>
          <i></i>
        </div>
      </div>
      <div class="right">
        <div
          class="title"
          :class="{currently: currentSongId === item.id}"
        >
          {{item.name}}
          <span
            class="aliases"
            v-for="alias in item.song.album.alias"
            :key="alias"
          >{{alias}}</span>
          <span class="info">
            <em v-if="item.song.bMusic.sr == 48000"></em>
            <!-- @todo一首个会有多个歌手 所以要遍历里面的 artists -->
            <i
              class="artist"
              :class="{currently: currentSongId === item.id}"
              v-for="(artist,index) in item.song.artists"
              :key="'artist1'+index"
            > - {{artist.name}}</i>
            <!-- @todo歌手和歌手之间用/分开 -->
            <b :class="{currently: currentSongId === item.id}">{{item.song.album.name}}</b>
          </span>
        </div>
      </div>
      <div
        class="deleted"
        @click.stop="$emit('delete-the-item')"
      ><svg
          class="icon"
          aria-hidden="true"
        >
          <use xlink:href="#icon-shanchu1"></use>
        </svg>
      </div>

    </li>

    <!-- song不存在，用新的数据 -->
    <!-- @error "Duplicate keys detected: '0'. This may cause an update error."检测到重复键:'0'。这可能会导致更新错误。 -->
    <!-- 出现这种错误的原因是，一个template中有两个一样的v-for -->
    <!-- @done 解决的办法是将其中一个key值修改一下避免重复 -->

    <li
      v-else
      class="songlistitem"
      @click.stop="$emit('change-current-song',item),$emit('add-history-play-list')"
    >
      <div
        class="icon-state"
        v-if="currentSongId === item.id"
      >
        <div
          class="play"
          :class="{current: currentSongId === item.id, playing: playing}"
        >
          <i></i>
          <i></i>
          <i></i>
          <i></i>
        </div>
      </div>
      <div class="right">
        <div
          class="title"
          :class="{currently: currentSongId === item.id}"
        >
          {{item.name}}
          <span
            class="aliases"
            v-for="alia in item.alia"
            :key="alia"
          >{{alia}}</span>
          <span class="info">
            <!-- <em v-if="item.song.bMusic.sr == 48000"></em> -->
            <!-- @todo一首个会有多个歌手 所以要遍历里面的 artists -->
            <i
              class="artist"
              v-for="(artist,index) in item.ar"
              :key="'artist2'+index"
              :class="{currently: currentSongId === item.id}"
            > - {{artist.name}}</i>
            <!-- @todo歌手和歌手之间用/分开 -->
            <b :class="{currently: currentSongId === item.id}">{{item.al.name}}</b>
          </span>
        </div>
      </div>
      <div
        class="deleted"
        @click.stop="$emit('delete-the-item')"
      ><svg
          class="icon"
          aria-hidden="true"
        >
          <use xlink:href="#icon-shanchu1"></use>
        </svg>
      </div>
    </li>
</template>

<script>
export default {
  data: function () {
    return {
    }
  },
  props: {
    item: {
      type: Object,
      required: true,
    },
    currentSongId: {
      type: Number,
    },
    playing: Boolean,
  },

}
</script>

<style lang="less" scoped>
// @done 用伪类 content: "/"; 可实现 ，再根据i标签中的last-of-type 选中最后一个艺人名使用 content:"-";连结专辑
// 注意：歌曲专辑 标签不可使用和艺人名标签相同的标签，这会影响到 last-of-type 选中最后一个


.songlistitem {
  // width:97%;
  height: 50px;
  padding: 13px 10px 13px 0;
  margin-left: 10px;
  // border-bottom: 1px solid rgba(212, 212, 212, 0.4);
  display: flex;
  align-items: center;
  transition: all 0.4s ease;
  .icon-state {
    width: 20px;
    height: 22px;
    margin-right: 2px;
    position: relative;
    padding-top: 2px;
    //   background-color: red;
    .play {
      width: 100%;
      height: 100%;
      // background-image: url(https://s3.music.126.net/mobile-new/img/index_icon_2x.png);
      background-repeat: no-repeat;
      background-size: 166px auto;
      background-position: -24px 0;
    }
    .current {
      width: 14px;
      height: 14px;
      background: red;
      background: none;
      display: flex;
      justify-content: space-around;
      position: absolute;
      left: 3px;
      i {
        width: 2px;
        height: 100%;
        background: red;
        transform-origin: bottom;
        animation: playing 0.8s linear 0s infinite alternate;
        animation-play-state: paused;
        // 四个不同的延迟
        &:nth-child(1) {
          animation-delay: -0.6s;
        }
        &:nth-child(2) {
          animation-delay: -0.4s;
        }
        &:nth-child(3) {
          animation-delay: -0.2s;
        }
      }
      &.playing {
        i {
          animation-play-state: running;
        }
      }
    }
  }
  .right {
    flex: 1;
    // @done 当设置为whidh:0;的时候，能避免子元素超出父元素,子容器恢复到设定的宽度，省略号也出现了
    width: 0;
    .title {
       width: 15.7rem;
      //  width: 100%;
      font-size: 15px;
      overflow: hidden;
      // @bug text-overflow: ellipsis；不生效，省略号没有出现，而且过长的文字把子容器撑开，没有按预设的超出隐藏。
      text-overflow: ellipsis;
      white-space: nowrap;
      word-break: normal;

      .aliases {
        font-size: 15px;
        margin-left: 4px;
        &::before {
          content: "(";
        }
        &::after {
          content: ")";
        }
      }
    }
    .currently {
      color: red;
      // transition: all 0.1s ease;
    }
    .info {
      font-size: 12px;
      color: #888;
      line-height: 1.9;
      em {
        width: 12px;
        height: 8px;
        // background-color: red;
        display: inline-block;
        background-image: url("https://s3.music.126.net/mobile-new/img/index_icon_2x.png");
        background-repeat: no-repeat;
        background-size: 166px;
        margin-right: 4px;
      }
      i.artist {
        // 取消字体倾斜
        font-style: normal;
        &:after {
          content: "/";
          margin: 0 4px;
        }
        &:last-of-type {
          //   background: red;
          &::after {
            content: "-";
            margin: 0 4px;
          }
        }
      }
      b {
        //   取消字体加粗
        font-weight: normal;
      }
    }
  }
  .deleted {
    width: 24px;
    height: 24px;
    padding: 4px;
    text-align: center;
    .icon {
      width: 0.9rem;
      height: 0.9rem;
      fill: #a9a9a9;
    }
  }
}
@keyframes playing {
  from {
    transform: scaleY(0.15);
  }
  to {
    transform: scaleY(0.85);
  }
}
</style>