import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Search from '../views/Search.vue'
import PlayList from '../views/PlayList.vue'



Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    meta: { showNavBar: true, index : 0, keepAlive : true},
  },
  {
    path: '/hot',
    name: 'Hot',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.

    // 懒加载
    component: () => import(/* webpackChunkName: "hot" */ '../views/Hot.vue'),
    meta: { showNavBar: true, index : 1, keepAlive : true},

  },
  {
    path: '/search',
    name: 'Search',
    component: Search,
    meta: { showNavBar: true, index : 2, keepAlive : true},

  },
  {
    path:'/playlist',
    name:'playList',
    component: PlayList,
    meta: {showNavBar: false, index : 3 , keepAlive : false},


  }
]

const router = new VueRouter({
  routes
})

export default router
