<template>
  <div>
    <section class="play-list">
      <div
        class="mask animate__animated animate__fadeIn"
        @click.stop="$emit('change-show-list',false)"
      ></div>
      <div class="swiper animate__animated animate__slideInUp">
        <div class="card">
          <div class="title-bar">
            <div class="play">
              <svg
                class="icon"
                aria-hidden="true"
              >
                <use xlink:href="#icon-bofang"></use>
              </svg>
            </div>
            <div class="title">当前播放<span>({{formerPlayList.length}})</span></div>
            <div
              class="ascending"
              v-if="!showrank"
              @click.stop="showrank = !showrank; $emit('rank-play-list')"
            >
              <svg
                class="icon"
                aria-hidden="true"
              >
                <use xlink:href="#icon-paixu-shengxu"></use>
              </svg>
            </div>
            <div
              class="inverted"
              v-if="showrank"
              @click.stop="showrank = !showrank; $emit('rank-play-list')"
            >
              <svg
                class="icon"
                aria-hidden="true"
              >
                <use xlink:href="#icon-paixu-jiangxu"></use>
              </svg>
            </div>
            <div
              class="del"
              @click.stop="showlist = true"
            >
              <svg
                class="icon"
                aria-hidden="true"
              >
                <use xlink:href="#icon-shanchu_delete"></use>
              </svg>
            </div>
          </div>
          <ul class="list">
            <!-- <transition-group name="flip-list"> -->
            <div
              class="new-list"
              v-for="(item,index) in formerPlayList"
              :key="item.id"
            >
              <div class="num">
                {{ (index + 1).toString().padStart(2, "0") }}
              </div>
                <SongListItem
                  class="current-list "
                  :item="item"
                  :currentSongId="currentSong.id"
                  :playing="playing"
                  @change-current-song="$emit('change-current-song',$event)"
                ></SongListItem>
            </div>
            <!-- </transition-group> -->
          </ul>
        </div>
      </div>

      <!-- 确定删除吗 -->
      <div
        class="inner-mask"
        v-if="showlist"
      ></div>
      <div
        class="inner animate__animated animate__slideInUp"
        v-if="showlist"
      >

        <p>确定要清空当前播放列表？</p>
        <div class="select">
          <span
            class="left"
            @click="showlist = false"
          >取消</span>
          <span
            class="right"
            @click="showlist = false; $emit('del-new-play-list')"
          >清空</span>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import SongListItem from '@/components/SongListItem.vue'
export default {
  data: function () {
    return {
      // 是否删除
      showlist: false,

      // 倒序顺序的显示
      showrank: false,
    }
  },
  components: {
    SongListItem
  },
  props: {
    showPlayList: Boolean,
    currentPlayList: Array,
    currentSong: Object,
    playing: Boolean,
    formerPlayList: Array,
  }
}
</script>

<style lang="less" scoped>
.flip-list-move {
  transition: all 0.15s ease;
}
.play-list {
  width: 100vw;
  height: 100vh;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 30;
  .mask {
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    position: absolute;
    left: 0;
    top: 0;
  }
  
  .swiper {
    width: 100vw;
    height: 60vh;
    position: absolute;
    left: 0;
    bottom: 38px;
    padding: 15px 8px;

    .card {
      background-color: white;
      height: 100%;
      border-radius: 15px;
      padding: 15px 10px;
      .title-bar {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 1px 0 10px 7px;
        .play {
          padding-top: 4px;
          .icon {
            width: 1.5rem;
            height: 1.5rem;
          }
        }
        .title {
          width: 100%;
          font-size: 20px;
          padding-left: 6px;
          span {
            font-size: 15px;
          }
        }
        .ascending,
        .inverted {
          margin-right: 29px;
          padding-top: 3px;
          .icon {
            width: 1.5rem;
            height: 1.5rem;
          }
        }
        .del {
          margin-right: 7px;
          padding-top: 4px;

          .icon {
            width: 1.5rem;
            height: 1.5rem;
          }
        }
      }
      .list {
        .new-list {
          display: flex;
          justify-content: space-around;
          align-items: center;
        }
        :nth-of-type(1),
        :nth-of-type(2),
        :nth-of-type(3) {
          .num {
            color: #d43c33;
          }
        }
        .current-list {
          flex: 1;
        }
      }
      ul {
        height: 90%;
        overflow: auto;
      }
    }
  }
  .inner {
    width: 330px;
    height: 116px;
    background-color: #fff;
    position: absolute;
    left: 22px;
    top: 300px;
    border-radius: 16px;
    z-index: 2;
    display: flex;
    text-align: center;
    flex-direction: column;
    justify-content: space-between;
    p {
      height: 68px;
      line-height: 68px;
      color: #000;
      padding: 0 15px;
      border-bottom: 1px solid #cdcdcd;
    }
    .select {
      height: 48px;

      // margin-top: 60px;
      // text-align: right;
      display: flex;
      justify-content: space-between;
      padding: 0 14px;
      span {
        width: 150px;
        height: 100%;
        line-height: 3;

        // margin-left: 80px;
      }
      .left {
        color: rgb(0, 110, 255);
      }
      .right {
        color: #ff3333;
        border-left: 1px solid #cdcdcd;
      }
    }
  }
  .inner-mask {
    content: "";
    display: inline-block;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.2);
    position: absolute;
    z-index: 1;
  }
}
</style>