<template>
  <section
    class="playbar"
    :class="{playing: playing}"
    @click="$emit('toggle-show-play-page', true),$emit('change-show-list',false)"
  >
    <img
      :src="
        `${
          currentSong.song ? currentSong.picUrl : currentSong.al.picUrl
        }?imageView=1&type=webp&thumbnail=60x0`"
      alt="music"
    >
    <!-- 在这里面想模仿SongListItem中的方式，将某些歌曲有多个作者的，遍历出来，可在一个元素上同时使用 v-if 和 v-for 时，v-for 会优先作用。 -->
    <!-- @bug 在处于同一节点的时候，v-for 优先级比 v-if 高。这意味着 v-if 将分别重复运行于每个 v-for 循环中。即——先运行v-for 的循环，然后在每一个v-for 的循环中，再进行 v-if 的条件对比。所以，不推荐v-if和v-for同时使用。 -->
    <h3>{{currentSong.name}} - <span
        v-for="(artist,i) in author"
        :key="i"
      >{{artist.name}}</span><i>{{album}}</i> </h3>
    <!--  @click.stop 阻止事件冒泡 -->
    <div
      class="progress"
      @click.stop="$emit('toggle-playing-state')"
    >
      <canvas
        width="40"
        height="40"
        ref="canvas"
      ></canvas>
      <div
        class="icon"
        :class="[playing ? 'pause' :'play']"
      ></div>
    </div>
    <div
      class="lists"
      @click.stop="toggleShowPlayList"
    ></div>
  </section>
</template>

<script>
export default {

  data: function () {
    return {
      // show: false,
    }
  },
  props: {
    currentSong: {
      type: Object,
    },
    playing: Boolean,
    currentTime: Number,
    duration: Number,
    currentPlayList: Array,
    author: Array,
    album: String,

  },
  methods: {
    toggleShowPlayList() {

      this.$emit('toggleShowPlayList')
    },
    drawImg(n) {
      var context = this.$refs.canvas.getContext("2d");
      context.clearRect(0, 0, 40, 40);
      context.beginPath();
      context.arc(
        20,
        20,
        19,
        (Math.PI / 180) * (0 - 90),
        (Math.PI / 180) * (360 * n - 90)
      );
      context.strokeStyle = "#d43c33";
      context.lineWidth = 1.6;
      context.stroke();
    }
  },
  mounted: function () {
    // console.log(this.$refs.canvas);
    this.drawImg();
  },
  computed: {
    percentage: function () {
      return this.currentTime / this.duration;
    },

  },
  watch: {
    percentage: function (n) {
      var context = this.$refs.canvas.getContext("2d");
      context.clearRect(0, 0, 40, 40);
      context.beginPath();
      context.arc(
        20,
        20,
        19,
        (Math.PI / 180) * (0 - 90),
        (Math.PI / 180) * (360 * n - 90)
      );
      context.strokeStyle = "#d43c33";
      context.lineWidth = 1.6;
      context.stroke();

      context.beginPath();
      context.arc(
        20,
        20,
        19,
        (Math.PI / 180) * (360 * n - 90),
        (Math.PI / 180) * (360 - 90)
      );
      context.strokeStyle = "#333";
      context.lineWidth = 1;
      context.stroke();
    },
  }
};
</script>

<style lang="less" scoped>
.playbar {
  width: 100%;
  height: 50px;
  background-color: white;
  position: fixed;
  left: 0;
  bottom: 0;
  display: flex;
  padding: 0 10px;
  //   justify-content: space-between;
  align-items: center;
  box-shadow: 0 -1px 2px rgba(0, 0, 0, 0.35);
  z-index: 100;
  border-top-left-radius: 6px;
  border-top-right-radius: 6px;

  img {
    width: 36px;
    height: 36px;
    border-radius: 50%;
    animation: rotate 8s linear infinite;
    animation-play-state: paused;
    box-shadow: 0 0 2px 2px black;
  }
  h3 {
    margin: 10px;
    flex: 1;
    font-size: 14px;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 1;
    overflow: hidden;
    text-overflow: ellipsis;
    span {
      color: #888;
      font-size: 12px;
      &::after {
        content: "/";
        margin: 0 4px;
      }
      &:last-of-type {
        //   background: red;
        &::after {
          content: "-";
          margin: 0 4px;
        }
      }
    }
    i {
      font-style: normal;
      font-size: 12px;
      color: #666;
    }
  }
  .progress {
    width: 30px;
    height: 30px;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    canvas {
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
    }
    .icon {
      width: 12px;
      height: 12px;
      display: flex;
      justify-content: space-around;
      align-items: center;
      &.play {
        border-top: 6px solid transparent;
        border-bottom: 6px solid transparent;
        border-left: 10px solid rgb(0, 0, 0);
        transform: translateX(2px);
      }
      &.pause {
        &::before,
        &::after {
          content: "";
          display: block;
          height: 80%;
          width: 20%;
          background-color: black;
        }
      }
    }
  }
  .lists {
    margin-left: 13px;
    width: 29px;
    height: 29px;
    background: url(../assets/lists.png) no-repeat;
    background-size: 28px;
  }

  &.playing {
    img {
      animation-play-state: running;
    }
  }
}
</style>