<template>
  <!-- @todo 判断item里面的song存不存在，存在用song -->
  <li
    v-if="item.song"
    class="songlistitem"
    @click="$emit('change-current-song',item)"
  >
    <div class="left">
      <div class="title">
        {{item.name}}
        <!-- 第一种写法 不需要通过伪元素添加"()" -->
        <!-- <span v-if="item.song.album.alias[0]">({{item.song.album.alias[0]}})</span> -->

        <!-- 第二种写法 -->
        <span
          v-for="alias in item.song.album.alias"
          :key="alias"
        >{{alias}}</span>
      </div>
      <div class="info">
        <em v-if="item.song.bMusic.sr == 48000 || item.fee == 8"></em>
        <!-- @todo一首个会有多个歌手 所以要遍历里面的 artists -->
        <i
          class="artist"
          v-for="(artist,index) in item.song.artists"
          :key="'artist1'+index"
        >{{artist.name}}</i>
        <!-- @todo歌手和歌手之间用/分开 -->
        <b>{{item.song.album.name}}</b>
      </div>
    </div>
    <div class="icon">
      <div
        class="play"
        :class="{current: currentSongId === item.id, playing: playing}"
      >
        <i></i>
        <i></i>
        <i></i>
        <i></i>
      </div>
    </div>
  </li>

  <!-- song不存在，用新的数据 -->
  <!-- @error "Duplicate keys detected: '0'. This may cause an update error."检测到重复键:'0'。这可能会导致更新错误。 -->
  <!-- 出现这种错误的原因是，一个template中有两个一样的v-for -->
  <!-- @done 解决的办法是将其中一个key值修改一下避免重复 -->
  <li
    v-else
    class="songlistitem"
    @click="$emit('change-current-song',item)"
  >
    <div class="left">
      <div class="title">
        {{item.name}}
        <span
          v-for="alia in item.alia"
          :key="alia"
        >{{alia}}</span>
      </div>
      <div class="info">
        <em v-if="item.fee == 8"></em>
        <!-- <em v-if="item.song.bMusic.sr == 48000"></em> -->
        <!-- @todo一首个会有多个歌手 所以要遍历里面的 artists -->
        <i
          class="artist"
          v-for="(artist,index) in item.ar"
          :key="'artist2'+index"
        >{{artist.name}}</i>
        <!-- @todo歌手和歌手之间用/分开 -->
        <b>{{item.al.name}}</b>
      </div>
    </div>
    <div class="icon">
      <div
        class="play"
        :class="{current: currentSongId === item.id, playing: playing}"
      >
        <i></i>
        <i></i>
        <i></i>
        <i></i>
      </div>
    </div>
  </li>
</template>

<script>
export default {
  props: {
    item: {
      type: Object,
      required: true,
    },
    currentSongId: {
      type: Number,
    },
    playing: Boolean,
  },
}
</script>

<style lang="less" scoped>
// @done 用伪类 content: "/"; 可实现 ，再根据i标签中的last-of-type 选中最后一个艺人名使用 content:"-";连结专辑
// 注意：歌曲专辑 标签不可使用和艺人名标签相同的标签，这会影响到 last-of-type 选中最后一个
.songlistitem {
  padding: 5px 10px 5px 0;
  margin-left: 10px;
  border-bottom: 1px solid rgba(212, 212, 212, 0.4);
  display: flex;
  align-items: center;
  .left {
    flex: 1;
    // @done 当设置为whidh:0;的时候，能避免子元素超出父元素,子容器恢复到设定的宽度，省略号也出现了
    width: 0;
    .title {
      font-size: 16px;
      overflow: hidden;
      // @bug text-overflow: ellipsis；不生效，省略号没有出现，而且过长的文字把子容器撑开，没有按预设的超出隐藏。
      text-overflow: ellipsis;
      white-space: nowrap;
      word-break: normal;

      span {
        font-size: 16px;
        color: #888;
        margin-left: 4px;
        &::before {
          content: "(";
        }
        &::after {
          content: ")";
        }
      }
    }
    .info {
      font-size: 12px;
      color: #888;
      line-height: 1.9;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 1;
      overflow: hidden;
      text-overflow: ellipsis;
      em {
        width: 12px;
        height: 8px;
        // background-color: red;
        display: inline-block;
        background-image: url("https://s3.music.126.net/mobile-new/img/index_icon_2x.png");
        background-repeat: no-repeat;
        background-size: 166px;
        margin-right: 4px;
      }
      i.artist {
        // 取消字体倾斜
        font-style: normal;
        &:after {
          content: "/";
          margin: 0 4px;
        }
        &:last-of-type {
          //   background: red;
          &::after {
            content: "-";
            margin: 0 4px;
          }
        }
      }
      b {
        //   取消字体加粗
        font-weight: normal;
      }
    }
  }
  .icon {
    width: 22px;
    height: 22px;
    margin-left: 15px;
    position: relative;
    //   background-color: red;
    .play {
      width: 100%;
      height: 100%;
      background-image: url(https://s3.music.126.net/mobile-new/img/index_icon_2x.png);
      background-repeat: no-repeat;
      background-size: 166px auto;
      background-position: -24px 0;
    }
    .current {
      width: 15px;
      height: 15px;
      background: red;
      background: none;
      display: flex;
      justify-content: space-around;
      position: absolute;
      left: 3px;
      i {
        width: 2px;
        height: 100%;
        background: #d43c33;
        transform-origin: bottom;
        animation: playing 0.8s linear 0s infinite alternate;
        animation-play-state: paused;
        // 四个不同的延迟
        &:nth-child(1) {
          animation-delay: -0.6s;
        }
        &:nth-child(2) {
          animation-delay: -0.4s;
        }
        &:nth-child(3) {
          animation-delay: -0.2s;
        }
      }
      &.playing {
        i {
          animation-play-state: running;
        }
      }
    }
  }
}
@keyframes playing {
  from {
    transform: scaleY(0.15);
  }
  to {
    transform: scaleY(0.95);
  }
}
</style>