<template>
  <div
    class="search"
    @scroll="scroll"
  >
    <div class="search-box">
      <div class="search-cover">
        <i
          class="search-icon"
          @click="$emit('')"
        ><svg
            class="icon"
            aria-hidden="true"
          >
            <use xlink:href="#icon-sousuo"></use>
          </svg>
        </i>
        <input
          type="text"
          placeholder="搜索歌曲、歌手、专辑"
          v-model.trim="value"
          @keyup.enter="value && (searching =true); isLoading = true; saveSearch()"
          @focus="inputing = true"
          @blur="inputing = false"
        >
        <i
          class="search-del"
          @click="value = '';searching = false;"
        >
          <span v-show="value"><svg
              class="icon"
              aria-hidden="true"
            >
              <use xlink:href="#icon-jiufuqianbaoicon08"></use>
            </svg></span>
        </i>
      </div>

    </div>

    <!-- 热门搜索推荐 -->
    <section
      class="hots"
      v-if="!value && !searching"
    >
      <h5>热门搜索</h5>
      <ul>
        <li
          class="hot-search"
          v-for="hot in hots"
          :key="hot.fitst"
          @click="searching = true; value = hot.first;isLoading = true; saveSearch()"
        >{{hot.first}}</li>
      </ul>

      <!-- 历史搜索记录 -->
      <ol>
        <transition-group
            name="list-complete"
            mode="out-in"
        >
        <li
          class="history-search"
          v-for="(h,i) in searchHistory"
          :key="h"
          @click="searching = true; value = h;isLoading = true; saveSearch()"
        >
          <i class="history-icon"><svg
              class="icon"
              aria-hidden="true"
            >
              <use xlink:href="#icon-lishiyouxibitongjiweijihuo"></use>
            </svg></i>
          <div class="history-content">
            <p>{{h}}</p>
            <i class="history-del" @click.stop="delSearchHistory(i)"><svg
                class="icon"
                aria-hidden="true"
              >
                <use xlink:href="#icon-shanchu1"></use>
              </svg></i>
          </div>
        </li>
        </transition-group>
      </ol>

    </section>

    <!-- 搜索建议 -->
    <section
      class="suggests"
      v-if="value && !searching"
    >
      <h5 @click="searching = true; isLoading = true; saveSearch()">搜索"{{this.value}}"</h5>
      <ul>
        <li
          class="suggest-search"
          v-for="(item, index) in suggests"
          :key="index"
          @click="searching = true; value = item.keyword; isLoading = true; saveSearch()"
        ><i class="suggests-icon">
            <svg
              class="icon"
              aria-hidden="true"
            >
              <use xlink:href="#icon-sousuo"></use>
            </svg>
          </i>
          <span>
            {{item.keyword}}
          </span>
        </li>
      </ul>
    </section>

    <!-- 搜索结果 -->
    <section
      class="result"
      v-if="searching &&isLoading == false"
      :class="{'paddingBottom':currentSong}"
    >
      <h5>搜索结果</h5>
      <ul>
        <!-- <li
          v-for="(item,index) in searchResults"
          :key="index"
        >{{item.name ? item.name : item.keyword}}</li> -->

        <li
          v-for="(item,index) in searchResults"
          :key="index"
          class="songlistitem"
          @click="setSearchPlay(item)"
        >
          <div class="left">
            <div class="title">
              {{item.name}}
              <span v-if="(item.alias != '')">{{item.alias[0]}}</span>
            </div>
            <div class="info">
              <em v-if="item.fee == 8"></em>
              <!-- @todo一首个会有多个歌手 所以要遍历里面的 artists -->
              <i
                class="artist"
                v-for="(artist,index) in item.artists"
                :key="'artist1'+index"
              >{{artist.name}}</i>
              <!-- @todo歌手和歌手之间用/分开 -->
              <b>{{item.album.name}}</b>
            </div>
          </div>
          <div class="icon">
            <div
              class="play"
              :class="{current: currentSongId === item.id, playing: playing}"
            >
              <i></i>
              <i></i>
              <i></i>
              <i></i>
            </div>
          </div>
        </li>
      </ul>
      <p
        class="loading-more"
        v-if='hasMore'
      > <img src="../assets/loading.gif">正在加载...</p>

      <p
        class="no-more"
        v-if='!hasMore'
      >全部加载完</p>
    </section>
    <div
      class="loading"
      v-show="isLoading == true"
    ><img
        src="../assets/loading.gif"
        alt="loading"
      >
      <p>加载中...</p>
    </div>
  </div>
</template>

<script>


export default {

  props: {
    currentSong: {
      type: Object,
    },
    currentSongId: Number,
    playing: Boolean,
  },
  data: function () {
    return {
      hots: [],
      suggests: [],
      // 搜索结果
      searchResults: [],
      value: "",
      // @todo searching判断是否显示 "搜索结果" 页面
      searching: false,
      // @todo 判断是否正在输入(是否有焦点),这是为了区分点击热门搜索点击，还是正在输入的
      inputing: false,

      page: 0,

      // 判断是否还有更多
      hasMore: false,
      // parse解析 searchistory第一次拿的时候为空 null,不存在取空；
      searchHistory: [],
      padBottom: "0px",
      isLoading: false,
    }
  },
  created: function () {
    this.axios.get("/search/hot").then((res) => {
      this.hots = res.data.result.hots;
      // console.log(this.hots);
    })
  },
  watch: {
    value: function (n) {
      // 判断this.inputing如果它为真，说明它正在输入中，就把searching改为false
      if (this.inputing) {
        this.searching = false;
      }
      // 输入框要有值
      if (n && !this.searching) {
        this.axios.get("/search/suggest", {
          params: {
            keywords: n,
            type: "mobile",
          }
        }).then((res) => {
          this.suggests = res.data.result.allMatch;
          console.log(this.suggests);
        });
      } else {
        this.suggests = [];
      }
    },
    searching: function (n) {
      if (n && this.value) {
        this.getSearch();
        this.searchHistory = JSON.parse(window.localStorage.getItem('history'));
      } else {
        this.searchResults = [];
      }
    },
  },
  methods: {
    scroll: function (event) {
      // console.log(event);
      // Math.ceil(),有小数就向上取整
      if (this.hasMore) {

        var h = event.target.offsetHeight + Math.ceil(event.target.scrollTop);
        if (h === event.target.scrollHeight) {
          // console.log("触底了");
          this.getSearch();
        }
      }
      // else {
      //   console.log("没有更多了");
      // }

    },
    getSearch: function () {
      this.axios.get("/search", {
        params: {
          keywords: this.value,
          limit: 30,
          offset: this.page * 30,

        }
      }).then(res => {
        // console.log(res.data.result.songs);
        if (res.data.result.songs == undefined || res.data.result.songs == '') {
          // this.searchResults.push("没有找到合适的");
          var a = `${this.value.slice(0, 4)}`;
          console.log(a);
          this.unSearch(a);
        } else {
          this.searchResults.push(...res.data.result.songs);
        }
        this.page++;
        this.hasMore = res.data.result.hasMore;
        this.loaded();
      });
    },
    unSearch: function (n) {
      this.axios.get("/search", {
        params: {
          keywords: n,
          limit: 20,
        }
      }).then((res) => {
        this.searchResults = res.data.result.songs;
        this.loaded();
      });
    },
    saveSearch() {
      // 记录一下历史记录
      this.searchHistory.unshift(this.value);
      this.searchHistory = Array.from(new Set([...this.searchHistory, this.value]));
      if (this.searchHistory.length > 15) {
        this.searchHistory.length = 15;
      }
      // stringifyJSON字符串化，
      localStorage.setItem('history', JSON.stringify(this.searchHistory));
    },
    setSearchPlay(item) {
      console.log('搜索列表的数据结构不一样',item);
      item.al = item.album;
      item.al.picUrl = item.album.artist.img1v1Url;
      item.ar = item.artists;
      item.alia = item.alias
      // 这两个要注意先后顺序，要改变当前歌曲后，再将这个item push()到currentPlayList,不然会报重复key的 error
      this.$emit('change-current-song', item);
      this.$emit('push-current-play-list', item);
    },
    loaded() {
      setTimeout(() => {
        this.isLoading = false;
      }, 200)
    },
    delSearchHistory(index){
      console.log('当前要删除的，搜索历史记录下标',index);
      this.searchHistory.splice(index,1);
      localStorage.setItem('history', JSON.stringify(this.searchHistory));
    }
  },
  mounted() {
    if (JSON.parse(localStorage.getItem('history'))) {
      this.searchHistory = JSON.parse(localStorage.getItem('history')) || [];
    }
  },

};
</script>
<style lang="less" scoped>
.list-complete-enter {
  opacity: 0;
  // transform: translateY(10px);
  // margin-top: 20px;
  margin-right: 22px;
}
.list-complete-leave-to {
  opacity: 0;
}
.list-complete-leave-active {
  position: absolute;
}
.search {
  height: 100%;
  overflow: auto;
  padding-top: 40px;
  .search-box {
    height: 60px;
    // border-top: 1px solid #d3d3d3;
    border-bottom: 1px solid #e6e2e2;
    padding: 15px 10px;

    .search-cover {
      width: 100%;
      height: 30px;
      border-radius: 20px;
      background-color: #ebecec;
      display: flex;
      justify-content: space-evenly;
      align-items: center;

      .search-icon {
        width: 17px;
        padding-top: 5px;
        .icon {
          width: 1rem;
          height: 1rem;
          fill: #bdbcbc;
        }
      }
      input {
        width: 80%;
        line-height: 18px;
        font-size: 14px;
        background-color: #ebecec;
        color: #333;
        outline: none;
        border: none;
      }
      ::-webkit-input-placeholder {
        color: #bdbcbc;
      }
      .search-del {
        width: 17px;
        padding-top: 5px;
        .icon {
          width: 1.1rem;
          height: 1.1rem;
          fill: #bdbcbc;
        }
      }
    }
  }
  .hots {
    padding: 10px 0 0 10px;
    h5 {
      font-size: 13px;
    }
    ul {
      list-style: none;
      padding: 10px 0;
      display: flex;
      justify-content: start;
      flex-wrap: wrap;
      .hot-search {
        // display: inline-block;
        height: 31px;
        border: 1px solid #d43c33;
        border-radius: 16px;
        padding: 0px 12px;
        margin: 0 10px 10px 0;
        line-height: 31px;
        font-size: 14px;
        cursor: pointer;
        text-align: center;
      }
    }
    ol {
      list-style: none;
      .history-search {
        width: 100%;
        height: 45px;
        display: flex;
        align-items: center;
        transition: all 0.5s ease;
        .history-icon {
          width: 15px;
          height: 15px;
          margin: 5px 12px 10px 0;
          .icon {
            width: 1.2rem;
            height: 1.2rem;
            fill: #bdbcbc;
          }
        }
        .history-content {
          height: 100%;
          line-height: 45px;
          flex: 1;
          display: flex;
          justify-content: space-between;
          align-items: center;
          border-bottom: 1px solid #e6e2e2;
          padding-right: 10px;

          p {
            // width: 89%;
            // height: 100%;
            font-size: 14px;
          }
          .history-del {
            // width: 20px;
            // height: 20px;
            padding: 6px 7px 0 7px;
            .icon {
              width: 1.1rem;
              height: 1.1rem;
              fill: #999999;
            }
          }
        }
      }
    }
  }
  .suggests {
    h5 {
      width: 100%;
      height: 50px;
      line-height: 50px;
      color: #507daf;
      border-bottom: 1px solid #e6e2e2;
      margin-left: 10px;
      padding-right: 10px;
    }
    ul {
      // padding: 10px 0;
      .suggest-search {
        height: 45px;
        line-height: 45px;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        border-bottom: 1px solid #e6e2e2;
        margin-left: 10px;
        padding-right: 10px;

        .suggests-icon {
          // width: 17px;
          // height: 17px;
          margin: 7px 8px 0 0;
          .icon {
            width: 1.1rem;
            height: 1.1rem;
            fill: #b1b1b2;
          }
        }
      }
    }
  }
  .result {
    padding-bottom: 0;
    ul {
      .songlistitem {
        padding: 5px 10px 5px 0;
        margin-left: 10px;
        border-bottom: 1px solid rgba(212, 212, 212, 0.4);
        display: flex;
        align-items: center;
        .num {
          width: 40px;
          margin-left: -10px;
          text-align: center;
          // margin: 0 10px 0 0;
        }
        .left {
          flex: 1;
          // @done 当设置为whidh:0;的时候，能避免子元素超出父元素,子容器恢复到设定的宽度，省略号也出现了
          width: 0;
          .title {
            font-size: 16px;
            overflow: hidden;
            // @bug text-overflow: ellipsis；不生效，省略号没有出现，而且过长的文字把子容器撑开，没有按预设的超出隐藏。
            text-overflow: ellipsis;
            white-space: nowrap;
            word-break: normal;

            span {
              font-size: 16px;
              color: #888;
              margin-left: 4px;
              &::before {
                content: "(";
              }
              &::after {
                content: ")";
              }
            }
          }
          .info {
            font-size: 12px;
            color: #888;
            line-height: 1.9;
            display: -webkit-box;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 1;
            overflow: hidden;
            text-overflow: ellipsis;
            em {
              width: 12px;
              height: 8px;
              // background-color: red;
              display: inline-block;
              background-image: url("https://s3.music.126.net/mobile-new/img/index_icon_2x.png");
              background-repeat: no-repeat;
              background-size: 166px;
              margin-right: 4px;
            }
            i.artist {
              // 取消字体倾斜
              font-style: normal;
              &:after {
                content: "/";
                margin: 0 4px;
              }
              &:last-of-type {
                //   background: red;
                &::after {
                  content: "-";
                  margin: 0 4px;
                }
              }
            }
            b {
              //   取消字体加粗
              font-weight: normal;
            }
          }
        }
        .icon {
          width: 22px;
          height: 22px;
          margin-left: 15px;
          position: relative;
          //   background-color: red;
          .play {
            width: 100%;
            height: 100%;
            background-image: url(https://s3.music.126.net/mobile-new/img/index_icon_2x.png);
            background-repeat: no-repeat;
            background-size: 166px auto;
            background-position: -24px 0;
          }
          .current {
            width: 15px;
            height: 15px;
            background: red;
            background: none;
            display: flex;
            justify-content: space-around;
            position: absolute;
            left: 3px;
            i {
              width: 2px;
              height: 100%;
              background: #d43c33;
              transform-origin: bottom;
              animation: playing 0.8s linear 0s infinite alternate;
              animation-play-state: paused;
              // 四个不同的延迟
              &:nth-child(1) {
                animation-delay: -0.6s;
              }
              &:nth-child(2) {
                animation-delay: -0.4s;
              }
              &:nth-child(3) {
                animation-delay: -0.2s;
              }
            }
            &.playing {
              i {
                animation-play-state: running;
              }
            }
          }
        }
      }
    }
    @keyframes playing {
      from {
        transform: scaleY(0.15);
      }
      to {
        transform: scaleY(0.95);
      }
    }
  }
  .loading-more {
    width: 100%;
    height: 2.6rem;
    display: flex;
    // flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    padding-top: 12px;
    img {
      width: 1.3rem;
      vertical-align: text-bottom;
    }
    p {
      font-size: 1rem;
      margin-left: 6px;
    }
  }
  .no-more {
    width: 100%;
    height: 2.1rem;
    line-height: 2.4;
    text-align: center;
    color: #666;
    font-size: 14px;
  }
  .loading {
    width: 100%;
    height: 5rem;
    display: flex;
    // flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    padding-top: 100px;
    img {
      width: 1.3rem;
      vertical-align: text-bottom;
    }
    p {
      font-size: 1rem;
      margin-left: 6px;
    }
  }
  .paddingBottom {
    padding-bottom: 60px;
  }
}
</style>
