<template>
  <div>
    <section
      class="lyrics_compact"
      v-if="currentLyric"
    >
      <div class="volume">
        <div class="progress-volume">
          <p class="volume-icon-lt">
            <span
              v-show="isShowVolume == 1"
              @click.stop="getBack"
            >
              <svg
                class="icon"
                aria-hidden="true"
              >
                <use xlink:href="#icon-_24gl-volumeZero"></use>
              </svg>
            </span>
            <span
              v-show="isShowVolume == 2"
              @click.stop="mute"
            >
              <svg
                class="icon"
                aria-hidden="true"
              >
                <use xlink:href="#icon-_24gl-volumeMiddle"></use>
              </svg>
            </span>
            <span
              v-show="isShowVolume == 3"
              @click.stop="mute"
            >
              <svg
                class="icon"
                aria-hidden="true"
              >
                <use xlink:href="#icon-a24gl-volumeHigh"></use>
              </svg>
            </span>
          </p>
          <div class="volume-box">
            <input
              type="range"
              :max="1"
              step="0.05"
              v-model="value"
              @input="progressVolumeChange"
            >
            <span
              class="volume-bar"
              :style="{width: (value / 1) * 100 + '%'}"
            ></span>
          </div>
          <p class="volume-icon-rt"><span>{{this.volumePercentum}}</span></p>
        </div>
      </div>

      <div
        class="lyric"
        @click.stop="$emit('toggle-show-lyric',false)"
      >
        <ul ref="lyricContent">
          <div
            class="lyric-load"
            v-show="lyricLoading"
          >
            歌词加载中...
          </div>
          <li
            v-for="(item, index) in currentLyric"
            :key="index"
            v-show="!lyricLoading"
          >
            <span :class="{ active : currentLyricIndex === index }">{{item.text}}</span>
          </li>

        </ul>
      </div>
    </section>

  </div>
</template>

<script>
export default {
  data: function () {
    return {
      lisHight: [],
      // 滚动高度
      hh: 0,
      // 没有歌词的每句滚动平均时间
      line: 0,
      // 为没有时间的歌词添加的
      curLine: 0,
      //当前时间是否为NaN
      conTime: 0,
      // inputing: 0,
      value: this.currentVolume,
      // 当前音量百分比
      isShowVolume: 2,
      currentVol: 0.5,

    }
  },
  props: {
    currentLyric: Array,
    currentTime: Number,
    currentVolume: Number,
    lyricLoading: Boolean,
    duration: Number,
    playing: Boolean,
  },
  mounted() {
    // console.log(this.currentLyric);
    // console.log(this.duration);
    // console.log(this.conTime);
    // console.log(this.currentLyric[0].time);
    this.conmentTime();
    this.notTimeHeight();
    this.getHeight();
    // this.$refs.lyricContent.style.marginTop = -this.hh + "px";

  },
  computed: {
    currentLyricIndex: function () {
      if (this.conTime === this.conTime) {
        var i = this.currentLyric.findIndex((item) => item.time > this.currentTime);
        var currentLyricIndex = i !== -1 ? i - 1 : this.currentLyric.length - 1;
        // console.log('222', currentLyricIndex);

      } else {
        // @bug 这里会有这样的问题，如果没有找到歌词，那当前歌词的时间也是NaN， 
        // @done 在主要为了性能优化来考虑，添加个判断条件，如果当前的歌词的长度大于1，
        // 才会执行计算curLine,否则，currentLyricIndex = 0;显示当前仅有的一句歌词。
        if (this.currentLyric.length > 1) {
          this.notTimeHeight();
          currentLyricIndex = this.curLine;
          // console.log("111", currentLyricIndex);
        } else {
          currentLyricIndex = 0;
        }
      }

      // @bug 比如说你点击下一首，切换了歌词，可currentLyricIndex=-1，体验很不好，
      // @done 添加个条件判断，看当前的currentLyricIndex是否等于-1，如果等于-1，则设置为0， 这样可以在一切换歌曲的时候，显示的是第一条歌词。
      if (currentLyricIndex === -1) {
        currentLyricIndex = 0;
      }
      return currentLyricIndex;
    },
    volumePercentum: function () {
      var volumeParcentum = Math.round(this.value / 1 * 100) + '%';
      // console.log('当前音量百分比',volumeParcentum);
      return volumeParcentum;
    }
  },
  watch: {
    currentLyricIndex: function (index) {
      // console.log('当前歌词', index);
      // 当前歌词前面所有的高度
      var h = this.lisHight.slice(0, index).reduce(function (total, item) {
        return total + item + 16.7;
      }, 0);
      this.hh = h;
      if (this.currentLyricIndex === 0) {
        this.hh = 0;
      }
      // console.log("当前歌词前面所有的高度", h);
      this.$refs.lyricContent.style.marginTop = -this.hh + "px";

    },
    // 侦听当前歌词是否有变化，如果发生变化，则调用conmentTime(),重新判断当前歌曲的时间是否为NaN
    currentLyric: function () {
      this.conmentTime();

    },
    //   currentLyric:function(){
    //       this.$nextTick(()=>{
    //           var lis = this.$refs.lyricContent.querySelectorAll("li");
    //           this.lisHight = [...lis].map((item)=>item.offsetHeight);
    //       })
    //   }
    value: function () {
      if (this.value == 0) {
        return this.isShowVolume = 1;
      }
      else if (this.value == 1) {
        return this.isShowVolume = 3;
      }
      else {
        return this.isShowVolume = 2;
      }
    },
    currentTime: function () {

    }
  },
  methods: {
    // 获取当前歌词前面的高度，
    getHeight() {
      var lis = this.$refs.lyricContent.querySelectorAll("li");
      // 整一首歌曲歌词的高度 lisHight
      this.lisHight = [...lis].map((item) => item.offsetHeight);
      // console.log('当前歌词的总高度', this.lisHight);
    },
    // 为解决有些歌词没有时间，而调用的方法。
    notTimeHeight() {
      // 当前总时长/当前歌词的长度
      this.line = Math.round(this.duration / this.currentLyric.length * 1000);
      this.curLine = Math.round(this.currentTime / this.line * 1000);
    },
    // 判断当前这首歌的时间，是不是NaN,
    conmentTime() {
      // 那么这里就查看当前歌词的第一句的时间，
      this.conTime = this.currentLyric[0].time;
      // console.log('当前歌词第一句的时间',this.conTime);

      // @todo 如何判断当前的歌词时间是否为NaN呢？isNaN()方法不够保险，假设一个字符串isNaN("A") // ture
      // @done 这里可以利用NaN不等于NaN的特殊性，NaN === NaN 为false
      // console.log('当前时间是不是NaN,是的话为false', this.conTime === this.conTime);
    },
    progressVolumeChange: function (event) {
      // console.log('当前进度条修改值', event.target.value);
      this.currentVol = event.target.value;
      this.$emit('current-volume-change', event.target.value);
    },
    mute() {
      this.isShowVolume = 1;
      this.value = 0;
      this.$emit('current-volume-change', 0);
    },
    getBack() {
      if (this.currentVol == 0) {
        this.isShowVolume = 1;
        this.currentVol = 0.5;
      } else {
        this.isShowVolume = 2;
      }
      this.value = this.currentVol;
      this.$emit('current-volume-change', this.currentVol);
    },

  },

  updated() {
    this.getHeight();

  },
  activated() {
    this.getHeight();
  },
  // beforeDestroy() {
  //   clearInterval(this.liTimer);
  //   this.liTimer = null;
  // }

}
</script>

<style lang="less" scoped>
@keyframes lyric-text {
  from {
    background-position-x: 0;
  }

  to {
    background-position-x: -100%;
  }
}
* {
  padding: 0;
  margin: 0;
}
.lyrics_compact {
  width: 100vw;
  height: 63vh;
  position: absolute;
  top: 4.4rem;
  left: 0;
  .volume {
    height: 2.7rem;
    // background-color: blueviolet;
    .progress-volume {
      width: 100%;
      height: 20px;
      position: absolute;
      top: 0.4rem;
      left: 0;
      display: flex;
      justify-content: space-around;
      align-items: center;
      padding: 0 11px 0 11px;
      .volume-icon-lt {
        width: 2rem;
        color: #fff;
        .icon {
          width: 1.35rem;
          height: 1.35rem;
          fill: #ffffff;
        }
        span {
          position: absolute;
          left: 1.8rem;
          top: -1px;
        }
      }
      .volume-box {
        width: 75%;
        height: 2px;
        position: relative;
        background-color: hsla(0, 0%, 100%, 0.7);
        border-radius: 10px;
        input {
          width: 100%;
          height: 100%;
          opacity: 0;
          position: absolute;
          z-index: 1;
          top: 0;
        }
        .volume-bar {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background-color: #fff;
          border-radius: inherit;
          &::before {
            content: "";
            width: 10px;
            height: 10px;
            display: block;
            margin-right: -5px;
            background-color: #fff;
            position: absolute;
            top: -4px;
            right: 0;
            border-radius: 50%;
            transform: all 0.1s ease;
          }
        }
      }
      .volume-icon-rt {
        width: 2rem;
        color: #fff;
        font-size: 12px;
        span {
          position: absolute;
          right: 18px;
          top: 3px;
        }
      }
    }
  }

  .lyric {
    width: 100%;
    height: 60.5vh;
    // background-color: skyblue;
    // color: rgba(214, 206, 206, 0.65);
    // color: hsla(0, 0%, 100%, 0.6);
    color: rgba(255, 255, 255, 0.5);
    overflow: hidden;
    ul {
      transition: all 0.4s ease;
      list-style: none;
      // @done 使用padding-top:;来代替吧,
      // transform: translateY(150px);
      padding-top: 168px;
      li {
        text-align: center;
        margin-top: 17px;
        span {
          display: inline-block;
          width: 75vw;
          line-height: 23px;
          font-size: 15px;
          // white-space: nowrap;
          word-break: keep-all;
          &.active {
            // color: #fff;
            color: rgb(255, 255, 255);
            font-size: 15px;
            transition: all 0.5s ease;
          }
        }
      }
      .lyric-load {
        width: 90%;
        height: 70px;
        // @bug 这里有个坑，fixed定位的元素，如果父级有transform样式，值不为none，那么fixed定位就会失效。
        // @todo 怎么解决呢？ 解决方法：使用transform样式的元素，不要包含fixed定位的子元素。
        position: fixed;
        top: 30%;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        margin: auto;
        color: #fff;
        font-size: 15px;
        text-align: center;
        // background-color: green;
      }
    }
  }
}
</style>