<template>
  <section
    class="playlist-box"
    ref="playList"
    @scroll="scroll"
    v-if="hotComments"
  >
    <!-- <div>PlayList {{$route.query.id}}</div> -->
    <div
      class="list-top"
      v-if="detail.creator"
    >
      <div
        class="mask"
        :style="{
        backgroundImage: `url(${
          detail.coverImgUrl
        }?imageView=1&type=webp&thumbnail=253x0)`,
      }"
      >

      </div>
      <span
        class="list-icon"
        @click='back'
      >
        <svg
          class="icon"
          aria-hidden="true"
        >
          <use xlink:href="#icon-fanhui3"></use>
        </svg>
        <svg
          class="icon"
          aria-hidden="true"
        >
          <use xlink:href="#icon-fenxiang1"></use>
        </svg>
      </span>
      <div class="list-content">
        <div class="img-box">
          <i class="song-list">歌单</i>
          <img
            class="list-img"
            :src="detail.coverImgUrl"
            alt="歌单详情"
          >
          <span class="parse-count">{{detail.playCount | parseCount }}</span>
        </div>
        <div class="song-title">
          <h2 class="list-name">{{detail.name}}</h2>
          <div class="list-author">
            <i><img
                :src="detail.creator.avatarUrl"
                alt=""
              ></i>
            <p>{{detail.creator.nickname}}</p>
          </div>
        </div>
      </div>
    </div>
    <div class="title">
      歌曲列表
    </div>
    <div
      class="playlist-box"
       v-if="!isLoading"
    >
      <ul>
        <div
          class="playlist"
          v-for="(item, index) in detail.tracks"
          :key="item.value"
        >
          <div class="num">
            {{index+1}}
          </div>
          <!-- @bug 初始化时，ajox是异步，在这里直接访问dom时，ajox还没拿完数据，detail一开始是个null -->
          <!-- @done 只有detail存在的时候才开始拿数据 v-if="detail" -->
          <!-- <li v-for="item in detail.tracks" :key="item.id">{{item.name}}</li> -->
          <SongListItem
            class="xxx"
            :item="item"
            :currentSongId="currentSongId"
            :playing="playing"
            @change-current-song="$emit('change-current-song',item),
             $emit('change-current-play-list',detail.tracks)"
          >
          </SongListItem>
        </div>
      </ul>
      <p class="client-side">查看更多歌曲， 请下载客户端</p>
    </div>
    <!-- 加载 -->
    <div
      class="loading"
      v-if="isLoading"
    ><img
        src="../assets/loading.gif"
        alt="loading"
      >
      <p>加载中...</p>
    </div>

    <!-- 最新评论 -->
    <section :class="{'paddingBottom': currentSong}">
    <div class="title">
      最新评论({{hotComments.length}})
    </div>
    <!-- 评论 -->
    <div
      class="new-comments animate__animated animate__fadeIn"
      v-for="item, index in hotComments"
      :key="index"
    >
      <div class="portrait">
        <i>
          <img :src="item.user.avatarUrl">
        </i>
      </div>
      <div class="content-box">
        <div class="pet-box">
          <div class="pet-name ">
            <p>{{item.user.nickname}} <i
                class="pet-vip"
                v-if="item.user.vipType == 11"
              ></i></p>
            <!-- 日期 -->
            <p class="pet-time">{{item.time | timestampToTime}}</p>
          </div>
          <div class="pet-icon">
            <span>{{item.likedCount}}</span>
            <i
              class="icon-one"
              v-if="!petLike"
            >
              <svg
                class="icon"
                aria-hidden="true"
              >
                <use xlink:href="#icon-31dianzan"></use>
              </svg>
            </i>
            <i
              class="icon-two"
              v-if="petLike"
            >
              <svg
                class="icon"
                aria-hidden="true"
              >
                <use xlink:href="#icon-dianzan_kuai"></use>
              </svg>
            </i>
          </div>
        </div>
        <p class="content-p">{{item.content}}</p>
      </div>
    </div>

    <!-- 加载更多 -->
    <div class="more">
      <p v-if="upload && more"><img src="../assets/loading.gif">正在加载...</p>
      <p
        @click="getMoreComment(); upload = true;"
        v-if="!upload && more"
      >查看更多评论:) ></p>
      <p v-if="!more">没有更多了:O</p>
    </div>

    <!-- 返回 -->
    <transition
      name="custom-classes-transition"
      enter-active-class="animate__animated animate__fadeInRight"
      leave-active-class="animate__animated animate__fadeOutRight"
    >
    <div
      class="back-top"
      v-if="showBack"
      @click="goBackTop()"
    >
      <svg
        class="icon"
        aria-hidden="true"
      >
        <use xlink:href="#icon-huidaodingbu1"></use>
      </svg>
    </div>
    </transition>
    </section>
  </section>

</template>

<script>

import SongListItem from "@/components/SongListItem.vue";
export default {

  // 在这里SongListItem需要注册
  components: {
    SongListItem,
  },
  props: {
    currentSongId: {
      type: Number,
    },
    currentSong: Object,
    playing: Boolean,
  },
  filters: {
    parseCount: function (value) {
      if (value > 100000000) {
        // @bug 当tofixed(1)保留一位小数的时候，1亿它会在小数点后补个0，显示为1.0亿
        // @done parseFloat()转换字符串成为浮点数,取整显示1亿 
        return parseFloat((value / 100000000).toFixed(1)) + "亿";
      } else if (value > 10000) {
        return parseFloat((value / 10000).toFixed(1)) + "万";
      } else {
        return value;
      }
    },
    timestampToTime(timestamp) {
      // var date = new Date(timestamp); //时间戳为10位需*1000，时间戳为13位的话不需乘1000
      // var Y = date.getFullYear() + '-';
      // var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
      // var D = date.getDate() + ' ';

      var date = new Date(timestamp);
      var Y = date.getFullYear();
      var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1);
      var D = date.getDate();
      // var h = date.getHours() + ':';
      // var m = date.getMinutes() + ':';
      // var s = date.getSeconds();
      return `${Y}年${M}月${D}日`;
    },
  },
  data() {
    return {
      detail: [],
      hotComments: [],
      page: 0,
      petLike: false,
      more: true,
      upload: false,
      showBack: false,
      isLoading:true,


    }
  },
  methods: {
    getPlayListDetail: function (id) {
      // @error 'canot read property "tracks" of null'
      this.axios
        .get("/playlist/detail", {
          params: {
            id,
          },
        })
        .then((res) => {
          // console.log(res);
          this.detail = res.data.playlist;
          setTimeout(() => {
          this.isLoading = false;
        }, 200)
        })
    },
    // https://apis.netstart.cn/music/comment/playlist/limit=20?id=4946207854
    // https://apis.netstart.cn/music/comment/playlist/limit=20/offset=20?id=4946207854
    getLatestReviews(id) {
      this.axios.get("/comment/playlist", {
        params: {
          limit: 20,
          offset: this.page * 20,
          id,
        },
      }).then((res) => {
        // console.log(res.data.hotComments);
        if (res.data.hotComments == undefined || res.data.hotComments == '') {
          this.hotComments.push(...res.data.comments);
        } else {
          this.hotComments.push(...res.data.hotComments);
        }
        this.page++;
        this.more = res.data.more;
        setTimeout(() => {
          this.upload = false;
        }, 600)

        this.$nextTick(() => {
          // console.log(this.$refs.playList.offsetHeight);
          // this.$refs.playList.scrollHeight / 3
          if (this.page < 2) {
            this.$refs.playList.scrollTop = 0;
          } else {
            this.$refs.playList.scrollTop = this.$refs.playList.scrollHeight - this.$refs.playList.offsetHeight;
          }
        })
      })
    },
    back() {
      if (window.history.length <= 1) {
        this.$router.push({ path: '/' });
        return false;
      } else {
        this.$router.go(-1);
      }
    },
    // giveLike() {
    //   this.petLike = !this.petLike;
    // }

    getMoreComment() {
      if (this.more) {
        this.getLatestReviews(this.$route.query.id);
      }
    },
    scroll: function (event) {
      var top = event.target.scrollTop;
      var h = event.target.offsetHeight;
      if (top > h) {
        this.showBack = true;
      } else {
        this.showBack = false;
      }
    },
    goBackTop(){
     var scrollTopTimer = setInterval(()=>{
        var top = this.$refs.playList.scrollTop;
      var speed = top / 35;
      if(this.$refs.playList.scrollTop !=0){
        this.$refs.playList.scrollTop -= speed;
      }else if(this.$refs.playList.scrollTop ==0){
       clearInterval(scrollTopTimer);
      }
     },1) 
    },
  },

  // 实例已经创建完成之后被调用
  created: function () {
    this.$nextTick(() => {
      this.getPlayListDetail(this.$route.query.id);
      this.getLatestReviews(this.$route.query.id);
    })
  },
  mounted() {
    this.$nextTick(() => {
      // console.log(this.comments);
    })
  }
};
</script>

<style lang="less" scoped>
.animate__animated {
  animation-duration: 0.3s;
}
* {
  margin: 0;
  padding: 0;
}
.list-top {
  width: 100%;
  height: 190px;
  position: relative;
  overflow: hidden;
  background-color: rgba(0, 0, 0, 0.34);
  .mask {
    // margin-top: 40px;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50%;
    filter: blur(20px);
    transform: scale(1.5);
    transition: all 0.3s ease;
    z-index: -1;
    ::after {
      content: " ";
      background-color: rgba(0, 0, 0, 0.1);
      position: absolute;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      z-index: 1;
    }
  }
  .list-icon {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 8px 10px 8px 10px;
    .icon {
      width: 1.2rem;
      height: 1.2rem;
      fill: #fff;
    }
  }
  .list-content {
    z-index: 10;
    display: flex;
    justify-content: space-between;
    // align-items: center;
    color: #fff;
    padding: 5px 15px 10px 15px;
    .img-box {
      width: 126px;
      height: 126px;
      position: relative;
      .song-list {
        position: absolute;
        top: 12px;
        left: 0px;
        height: 17px;
        padding: 0 8px;
        font-style: normal;
        font-size: 12px;
        background-color: rgba(217, 48, 48, 0.8);
        text-align: center;
        line-height: 17px;
        border-top-right-radius: 17px;
        border-bottom-right-radius: 17px;
        z-index: 3;
      }
      .parse-count {
        position: absolute;
        top: 0;
        right: 2px;
        font-size: 12px;
        z-index: 2;
        // transform: scale(0.9);
      }
      &::after {
        content: "";
        width: 100%;
        height: 18px;
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        background-image: linear-gradient(
          to bottom,
          rgba(0, 0, 0, 0.38),
          rgba(0, 0, 0, 0.15)
        );
      }
    }
    .song-title {
      flex: 1;
      margin: 0px 10px 5px 14px;
      .list-name {
        width: 100%;
        font-size: 16px;
      }
      .list-author {
        display: flex;
        align-items: center;
        margin-top: 10px;
        i {
          width: 32px;
          height: 32px;
          border-radius: 50%;
          overflow: hidden;
          border: 1px solid #fff;
        }
        p {
          width: 110px;
          margin-left: 6px;
          font-size: 14px;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          word-break: normal;
        }
      }
    }
  }
}
.title {
  width: 100%;
  height: 1.8rem;
  line-height: 2;
  background-color: #eeeff0;
  font-size: 14px;
  padding-left: 10px;
}
.playlist-box {
  padding-bottom: 0px;
  ul {
    .playlist {
      display: flex;
      justify-content: space-around;
      align-items: center;
      .num {
        width: 28px;
        font-size: 17px;
        margin-left: 6px;
        text-align: center;
        // margin: 0 10px 0 0;
      }
      .xxx {
        flex: 1;
      }
    }
    :nth-of-type(1),
    :nth-of-type(2),
    :nth-of-type(3) {
      .num {
        color: #d43c33;
      }
    }
  }
  .client-side{
    height: 25px;
    line-height: 2;
   text-align: center;
   font-size: 13px;
  }
}
.loading {
    width: 100%;
    height: 35rem;
    display: flex;
    // flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    padding-top: 80px;
    img {
      width: 1.3rem;
      vertical-align: text-bottom;
    }
    p {
      font-size: 1rem;
      margin-left: 6px;
    }
  }
.new-comments {
  display: flex;
  padding: 11px 10px;
  border-bottom: 1px solid rgba(212, 212, 212, 0.4);
  // margin-bottom: 30px;
  .portrait {
    width: 40px;
    i {
      display: block;
      width: 30px;
      height: 30px;
      border: 1px solid #fff;
      border-radius: 50%;
      overflow: hidden;
      margin-top: 8px;
    }
  }
  .content-box {
    flex: 1;
    .pet-box {
      display: flex;
      justify-content: space-between;
      padding: 5px 10px 8px 0;
      .pet-vip {
        display: inline-block;
        width: 32px;
        height: 11px;
        background-image: url(https://p6.music.126.net/obj/wo3DlcOGw6DClTvDisK1/4213922817/9124/a83c/7eb7/6d7d81b608bfb56d7fb286bd8eb72346.png);
        background-size: cover;
        background-position: 50%;
        background-size: 100%;
      }
      .pet-name {
        font-size: 14px;
        color: #666;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        -webkit-box-flex: 1;
        .pet-time {
          font-size: 12px;
          transform: scale(0.95);
          color: #999;
          margin-left: -2px;
          margin-top: 3px;
        }
      }
      .pet-icon {
        width: 50px;
        position: relative;
        span {
          position: absolute;
          top: 6px;
          right: 17px;
          font-size: 12px;
        }
        .icon-one {
          position: absolute;
          top: 2px;
          right: -4px;
          .icon {
            width: 1rem;
            height: 1rem;
          }
        }
        .icon-two {
          position: absolute;
          top: 2px;
          right: -4px;
          .icon {
            width: 1rem;
            height: 1rem;
            fill: #d43c33;
          }
        }
      }
    }
    .content-p {
      // 强制英文单词断行
      word-break: break-all;
      padding-right: 25px;
      font-size: 14px;
    }
  }
}
.more {
  width: 100%;
  height: 45px;
  line-height: 45px;
  text-align: center;
  p {
    font-family: "宋体";
    font-size: 14px;
    color: #000;
    img {
      width: 1.2rem;

      // 把元素的底端与父元素字体的底端对齐。
      vertical-align: text-bottom;
      
    }
  }
}
.back-top {
  width: 41px;
  height: 41px;
  background-color: #fff;
  border-radius: 50%;
  box-shadow: 1px 1px 4px rgba(51, 51, 51, 0.521);
  position: fixed;
  bottom: 90px;
  right: 50px;
  padding: 8px;
  .icon {
    width: 1.5rem;
    height: 1.5rem;
    fill: #333;
  }
}
.paddingBottom {
  padding-bottom: 50px;
}
</style>