  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  // text-align: left;
  color: #2c3e50;
  // @todo 解决添加尾部 "open-app" 出现页尾距离页面底部大约有20px的距离
  // @done 添加dispaly: flex; 弹性布局方向以列的形式
  display: flex;
  flex-direction: column;
  align-content: center;
}
