<template>
  <section class="play-page">
    <transition name="fade">
      <div
        v-show="showflag"
        class="mask"
        :style="{
        backgroundImage: `url(${
          currentSong.song ? currentSong.picUrl : currentSong.al.picUrl
        }?imageView=1&type=webp&thumbnail=246x0)`,
      }"
      ></div>
    </transition>

    <!-- 播放页面导航 -->
    <div class="playTop">
      <div
        class="back"
        @click.stop="$emit('toggle-show-play-page', false)"
      >
        <svg
          class="icon"
          aria-hidden="true"
        >
          <use xlink:href="#icon-fanhui"></use>
        </svg>
      </div>
      <div class="center">

        <!-- 跑马灯实现标签 -->
        <marquee
          behavior="scroll"
          direction="left"
          scrolldelay="250"
        >
          <div class="title">{{currentSong.name}}
            <span
              class="alias"
              v-if="showAlias"
            > {{alias}}</span><span> - 专辑 - ({{album}})</span>
            <!-- <span> - {{currentSong.song ? currentSong.song.artists[0].name : currentSong.ar[0].name}}</span> -->
          </div>
        </marquee>
      </div>

      <!-- 分享 -->
      <div class="share" @click.stop="showMessages({show :true,title:'👉下次一定...'})">
        <svg
          class="icon"
          aria-hidden="true"
        >
          <use xlink:href="#icon-fenxiang"></use>
        </svg>
      </div>
    </div>
    <div class="author"> <span
        v-for="(item, index) in author"
        :key="index"
      >{{item.name}}</span></div>
    <span
      class="attention"
      v-show="author.length < 2 "
    >+</span>
    <span
      class="authors"
      v-show="(author.length > 1) "
    >></span>

    <!-- 唱片部分 -->
    <transition
      name="custom-classes-transition"
      enter-active-class="animate__animated animate__fadeIn"
      leave-active-class="animate__animated animate__fadeOut"
    >
      <section
        name="rotateBox"
        class="rotate"
        v-if="!showLyric"
        @click.stop="showLyric = true"
      >
        <img
          class="needle"
          :class="{paused :!playing}"
          src="https://s3.music.126.net/mobile-new/img/needle-ab.png"
          alt=""
        >
        <section
          class="record"
          :class="{playing: playing}"
        >
          <img
            class="thumb"
            src="https://s3.music.126.net/mobile-new/img/disc_default.png"
            alt=""
          >
          <transition name="fade">
            <img
              v-show="showflag"
              class="thumb"
              :src="url"
              alt=""
            >
          </transition>
          <img
            class="disc"
            src="https://p5.music.126.net/obj/wo3DlcOGw6DClTvDisK1/4602256867/5855/63fb/325b/d7e4e3a244701ee85fecb5d4f6b5bd57.png"
            alt=""
          >
        </section>
        <!-- 收藏 下载... -->

        <section class="operation">
          <!-- 喜欢添加到收藏 -->
          <span
            v-if="!currentSong.isLike"
            @click.stop="addLikeSong()"
            class="animate__animated animate__pulse"
          >
            <svg
              class="icon"
              aria-hidden="true"
            >
              <use xlink:href="#icon-xihuan3"></use>
              <!-- <use :xlink:href="[playing ? '#icon-xihuan3':'#icon-xihuan' ]"></use> -->
            </svg>
          </span>
          <span
            v-else
            @click.stop="removeLikeSong(), showMessages({show :true,title:'已取消喜欢'})"
            class="animate__animated animate__heartBeat "
          >
            <svg
              class="icon"
              aria-hidden="true"
            >
              <use xlink:href="#icon-xihuan"></use>
              <!-- <use :xlink:href="[playing ? '#icon-xihuan3':'#icon-xihuan' ]"></use> -->
            </svg>
          </span>
          <!-- 下载 -->
          <span @click.stop="showMessages({show :true,title:'🌞没有做...'})">
            <svg
              class="icon"
              aria-hidden="true"
            >
              <use xlink:href="#icon-changyongicon-"></use>
            </svg>
          </span>
          <!-- 唱 -->
          <span
            @click.stop="showMessages({show :true,title:'e🌝y没有做...'})"
            class="sing"
          >
            <svg
              class="icon"
              aria-hidden="true"
            >
              <use xlink:href="#icon-changge"></use>
            </svg>
            <i>999+</i>
          </span>
          <!-- 评论 -->
          <span
            @click.stop="showMessages({show :true,title:'😳这个也没有...'})"
            class="comment"
          ><svg
              class="icon"
              aria-hidden="true"
            >
              <use xlink:href="#icon-pinglun2"></use>
            </svg>
            <i>1w+</i>
          </span>
          <!-- 列表 -->
          <span @click.stop="showMessages({show :true,title:'🕞下次一定...'})">
            <svg
              class="icon"
              aria-hidden="true"
            >
              <use xlink:href="#icon-ellipsis2"></use>
            </svg>
          </span>
        </section>
      </section>
    </transition>

    <!-- 歌词部分 -->
    <transition
      name="custom-classes-transition"
      enter-active-class="animate__animated animate__fadeIn"
      leave-active-class="animate__animated animate__fadeOut"
    >
      <keep-alive>
        <Lyric
          v-if="showLyric"
          :currentLyric="currentLyric"
          :currentTime="currentTime"
          :currentVolume="currentVolume"
          :lyricLoading="lyricLoading"
          :duration="duration"
          :playing="playing"
          @toggle-show-lyric="showLyric = $event"
          @current-volume-change="$emit('current-volume-change', $event)"
        >
        </Lyric>
      </keep-alive>
    </transition>

    <!-- 进度条 -->
    <section class="progress">
      <p
        class="play-time-lt"
        :style="{'--ltColor':ltColor}"
      >{{curTime}}</p>
      <div
        class="progress-box"
        :style="{'--progressHeight':proHight}"
      >
        <input
          type="range"
          name=""
          id=""
          :max="duration"
          step="0.5"
          v-model="value"
          @change="progressChange"
          @input="progressInput"
        >
        <span
          class="bar"
          :style="{width: (value / duration) * 100 + '%','--barHight':barHight,'--barWidth':barWidth,'--barTop':barTop}"
        ></span>
      </div>

      <p class="play-time-rt">{{allTime}}</p>
    </section>

    <!-- 暂停，上一曲，下一曲 -->
    <section class="controls">
      <!-- 播放模式 -->
      <div @click="$emit('toggle-play-model')">
        <!-- 列表循环 -->
        <div
          v-show="playerMode === 1"
          @click="$emit('song-cycle')"
        >
          <svg
            class="icon"
            aria-hidden="true"
          >
            <use xlink:href="#icon-xunhuan"></use>
          </svg>
        </div>

        <!-- 单曲循环 -->
        <div
          v-show="playerMode === 2"
          @click="$emit('shuffle-play')"
        >
          <svg
            class="icon"
            aria-hidden="true"
          >
            <use xlink:href="#icon-_24gl-repeatOnce2"></use>
          </svg>
        </div>

        <!-- 随机播放 -->
        <div
          v-show="playerMode === 3"
          @click="$emit('list-cycle')"
        >
          <svg
            class="icon"
            aria-hidden="true"
          >
            <use xlink:href="#icon-suijibofang"></use>
          </svg>
        </div>
      </div>

      <!-- 上一曲 -->
      <div
        class="previous"
        @click="$emit('previous-song')"
      >
        <svg
          class="icon"
          aria-hidden="true"
        >
          <use xlink:href="#icon-shangyiqu101"></use>
        </svg>
      </div>
      <!-- 播放 -->
      <div
        class="play-state"
        @click="$emit('toggle-playing-state')"
      >
        <div
          class="pTmpLay"
          v-if="!playing"
        >
          <span class="auplay"></span>
        </div>
        <div
          class="suspended"
          v-else
        ></div>
      </div>
      <!-- 下一曲 -->
      <div
        class="exit-music"
        @click="$emit('next-song')"
      ><svg
          class="icon"
          aria-hidden="true"
        >
          <use xlink:href="#icon-xiayiqu101"></use>
        </svg>
      </div>
      <!-- 列表 -->
      <div @click="showPlayList = true">
        <svg
          class="icon"
          aria-hidden="true"
        >
          <use xlink:href="#icon-bofangliebiao"></use>
        </svg>
      </div>
    </section>
    <PagePlayList
      v-show="showPlayList"
      :currentPlayList="currentPlayList"
      :currentSong="currentSong"
      :playing="playing"
      :showPlayList="showPlayList"
      :collectionList="collectionList"
      :historyPlayList="historyPlayList"
      :presentPlayList="presentPlayList"
      @change-current-song="$emit('change-current-song',$event)"
      @change-show-list="showPlayList = $event"
      @delete-the-item="$emit('delete-the-item',$event)"
      @add-history-play-list="$emit('add-history-play-list')"
      @del-all-collection-list="$emit('del-all-collection-list')"
      @del-all-history-list="$emit('del-all-history-list')"
      @del-all-present-list="$emit('del-all-present-list')"
    ></PagePlayList>
    <Tips></Tips>
    <transition
      name="custom-classes-transition"
      enter-active-class="animate__animated animate__fadeInDown"
      leave-active-class="animate__animated animate__fadeOutUp"
    >
    <div
      class="li-tips-box"
      v-if="showTopTips"
    >
      <div class="like-tips">
        <i>
          <svg
            class="icon"
            aria-hidden="true"
          >
            <use xlink:href="#icon-xihuan"></use>
          </svg>
        </i>
        <p>已添加到我喜欢的音乐</p>
      </div>
    </div>
    </transition>

    <transition
      name="custom-classes-transition"
      enter-active-class="animate__animated animate__fadeInDown"
      leave-active-class="animate__animated animate__fadeOutUp"
    >
      <div
        class="disable-tips-box"
        v-if="messages.show"
      >
        <div class="disable-tips">
          <p>{{messages.title}}</p>
        </div>
      </div>
    </transition>
  </section>
</template>

<script>
import PagePlayList from '@/components/PagePlayList.vue';
import Lyric from '@/components/Lyric.vue';
import Tips from "@/components/Tips.vue";


export default {
  components: {
    PagePlayList,
    Lyric,
    Tips,
  },
  data: function () {
    return {
      showPlayList: false,
      showflag: false,
      showAlias: true,
      url: 'https://s3.music.126.net/mobile-new/img/disc_default.png',
      lyric: null,
      showLyric: false,
      lyricLoading: true,
      lyTimer: null,
      value: this.currentTime,
      inputing: false,

      proHight: "2px",
      barHight: "10px",
      barWidth: "10px",
      barTop: "-4px",
      ltColor: "#ffffff",

      curTime: '00:00',
      allTime: '00:00',
      isLike: false,
      showTopTips:false,
      topTimer: '',

      // 取消提示
      messages: {
        show: false,
        timer2: '',
        title: '已取消喜欢',
      },
      flag: true,
    }
  },
  props: {
    currentSong: Object,
    playing: Boolean,
    currentTime: Number,
    duration: Number,
    currentPlayList: Array,
    author: Array,
    album: String,
    playerMode: Number,
    currentVolume: Number,
    collectionList: Array,
    historyPlayList: Array,
    presentPlayList: Array,
  },
  watch: {
    "currentSong.id": function (id) {
      this.getLyric(id);
      this.lyricLoading = true;
      this.calculateTinme();

    },
    currentTime: function (n) {
      this.curTime = this.formatTime(this.currentTime);

      // 判断是否拖拽歌词进度条，
      if (!this.inputing) {
        this.value = n;
        this.proHight = 2 + "px";
        this.barHight = 10 + "px";
        this.barWidth = 10 + "px";
        this.barTop = -4 + "px";
        this.ltColor = "#d3d3d3";
      } else {
        this.proHight = 5 + "px";
        this.barHight = 15 + "px";
        this.barWidth = 15 + "px";
        this.barTop = -5 + "px";
        this.ltColor = "#ffffff";

      }
    },
    duration: function () {
      this.allTime = this.formatTime(this.duration);
    }
  },
  computed: {
    alias: function () {
      if (this.currentSong.song) {
        return this.currentSong.song.alias[0];
      } else {
        return this.currentSong.alia[0];

      }
    },
    currentLyric: function () {
      if (this.lyric) {
        // @bug 这里有个问题就是，有些歌词当中是没有时间段的，有的一句歌词又有好几个时间段的，这同一个方法拆分歌词，会出错，
        // 这时候可以通过是否含有"[", "]" 来决定使用那种方式拆分歌词。
        // @done 通过正则表达式来判断this.lyric当中的歌词是否有时间，如果有就走下面的，
        var tegeX = /^\[|\]$/g;
        var a = tegeX.test(this.lyric);
        // console.log('当前歌词是否有时间', a);
        if (!a) {
          console.log("处理歌词方法1", 1111);
          return this.lyric.split("\n").filter((s) => s).map((s) => {
            var text = s.replace(/(?:\[[^\]]*\])/, "");
            var timeStr = s.replace(text, "").replace(/(^\[|\]$)/g, "");
            var timeArr = timeStr.split(":").map((item) => Number(item));
            var time = timeArr[0] * 60 + timeArr[1];
            if (text === "" || text === " ") {
              text = "......";
            }
            return { text, time };
          })
        } else {

          // @bug 这里有个问题，是当中一句歌词有好几段时间，这时按照上111的方式拆出来的歌词有包含[00:00.000]的样式
          // @done 使用第二种方法解析多个事件段的歌词，非常友好。
          console.log("处理歌词方法2", 2222);
          var lyricStr = this.lyric.replace(/\]\[/g, '] [');
          var lyricArr = lyricStr.match(/(\[\d{2}:\d{2}\.\d{2,3}\])(.[^[\]]*)?/g);
          var arr = [];
          // console.log(lyricStr);
          lyricArr.forEach((item) => {
            var object = {};
            /^(\[\d{2}:\d{2}\.\d{2,3}\])(.[^[\]]*)?$/.exec(item);
            var timeStr = (RegExp.$1).replace('[', '').replace(']', '');
            var timeArr = timeStr.split(':').map((item) => Number(item));
            var time = timeArr[0] * 60 + timeArr[1];
            var text = (RegExp.$2).replace(/[\r\n]/g, "");
            object['time'] = time;
            object['text'] = text;
            arr.push(object);
          })
          arr.forEach((item, index, arr1) => {
            if (item.text === '') {
              // console.log(index);
              arr1.splice(index, 1);
              index = index - 1;
            }
          })
          arr.forEach((item, index, arr1) => {
            if (item.text === '' || item.text === ' ') {
              arr1.splice(index, 1);
              index = index - 1;
            }
          })
          return arr;
        }
      } else {
        return [{ 'text': '没有找到歌词...', 'time': NaN }];
      }
    },


  },
  methods: {
    changeImgs() {
      // 加载完再显示图片
      var playImg = new Image();
      playImg.src = this.currentSong.song ? this.currentSong.picUrl : this.currentSong.al.picUrl;
      playImg.onload = () => {
        this.url = playImg.src;
        setTimeout(() => {
          this.showflag = true;
        }, 300)
      }
    },
    changeAlias() {
      // 判断alias是否为空，
      if (this.alias == undefined || this.alias == '') {
        this.showAlias = false;
      } else {
        this.showAlias = true;
      }
    },

    // 获取歌词
    getLyric: function (id) {
      this.axios
        .get("/lyric",
          {
            params: { id },
          })
        .then((res) => {
          // this.lyric = res.data.lrc ? res.data.lrc.lyric : "纯音乐，请欣赏"
          // this.lyric = res.data.lrc.lyric == '' ? "纯音乐，请欣赏" : res.data.lrc.lyric
          this.lyricEstimate(res);
          this.lyricLoaded();
        });
    },
    // 判断成功请求到的数据，是否是空的或者lrc是不存在的
    lyricEstimate(res) {
      if (res.data.lrc == undefined || res.data.lrc.lyric == '') {
        this.lyric = "纯音乐，请欣赏.";
      } else {
        this.lyric = res.data.lrc.lyric;
      }
    },
    lyricLoaded() {
      clearTimeout(this.lyTimer);
      this.lyTimer = setTimeout(() => {
        this.lyricLoading = false;
      }, 1000)
    },
    // 改变播放模式
    changePlayerModel() {
      // console.log(111);
    },
    progressChange: function (event) {
      this.inputing = false;
      // console.log('当前进度条修改值', event.target.value);
      this.$emit('current-time-change', event.target.value)
    },
    progressInput: function () {
      this.inputing = true;
    },
    formatTime(time) {
      if (time == 0) {
        this.curTime = '00:00'
      }
      const mins = Math.floor(time / 60) < 10 ? `0${Math.floor(time / 60)}` : Math.floor(time / 60);
      const sec = Math.floor(time % 60) < 10 ? `0${Math.floor(time % 60)}` : Math.floor(time % 60);
      return `${mins}:${sec}`;
    },
    calculateTinme() {
      this.curTime = this.formatTime(this.currentTime);
      this.allTime = this.formatTime(this.duration);
    },
    addLikeSong() {
      this.isLike = true;
      this.showTopTips = true;
      this.messages.show  = false;
      this.$emit('add-collection', this.isLike);
      this.showLikeTips();
    },
    removeLikeSong() {
      this.isLike = false;
      this.showTopTips = false;
      this.$emit('cancel-collection', this.isLike)
    },
    showLikeTips() {
      clearTimeout(this.topTimer);
      if(this.showTopTips){
        this.topTimer = setTimeout(() =>{
          this.showTopTips = false;
        }, 1300)
      }
    },
    showMessages(data){
      // console.log('当前要设置的title内容',data);

      // 添加个节流阀
      if(this.flag){
        this.flag = false;
        clearTimeout(this.timer);
        this.messages = data;
        if(this.messages.show){
            this.timer = setTimeout(() =>{
            this.messages.show = false;
            this.flag = true;
          }, 1300)
        }
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this.getLyric(this.currentSong.id);
    })
  },
  mounted() {
    this.$nextTick(() => {
      this.changeImgs();
      this.calculateTinme();
    })
    // console.log(this.currentMode);

  },
  updated() {
    this.$nextTick(() => {
      this.changeImgs();
      this.changeAlias();
    })
  },
  activated() {
    this.$nextTick(() => {
      this.changeImgs();
      this.changeAlias();
    })
  }
  // beforeUpdate() {
  //   // 加载完再显示图片
  //   var playImg = new Image();
  //   playImg.src = this.currentSong.song ? this.currentSong.picUrl : this.currentSong.al.picUrl;
  //   playImg.onload = () => {
  //     this.showflag = true;
  //     this.url = playImg.src;
  //   }
  //   // 判断alias是否为空，
  //   if (this.alias == undefined) {
  //     this.showAlias = false;
  //   }
  // }
}
</script>

<style lang="less" scoped>
.animate__fadeIn,
.animate__fadeOut {
  animation-duration: 0.2s;
}
.animate__heartBeat {
  animation-duration: 1.6s;
}
.pos-ab() {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.4s;
  // transition-delay: 1s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
.play-page {
  width: 100vw;
  height: 100vh;
  position: fixed;
  left: 0;
  top: 0;
  // background-color: rgba(129, 124, 124, 0.7);
  z-index: 90;
  &::before {
    content: "";
    display: block;
    .pos-ab();
    z-index: -1;
    background-color: #666;
  }
  .mask {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    // background-color: white;
    filter: blur(65px) brightness(0.85);
    transition: background-image 0.3s ease;
    .pos-ab();
    z-index: -1;
  }
  .playTop {
    width: 100%;
    height: 68px;
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-align: center;
    line-height: 50px;
    color: #fff;
    padding: 0 27px;
    margin-top: 5px;
    .icon {
      display: block;
      width: 1.52rem;
      height: 1.52rem;
      fill: white;
      font-weight: 500;
    }
    .center {
      width: 68%;
      height: 100%;
      display: flex;
      marquee {
        display: block;
        flex: 1;
        .title {
          font-size: 16px;
          .alias {
            font-size: 16px;
            &::before {
              content: "(";
            }
            &::after {
              content: ")";
            }
          }
        }
      }
    }
  }
  .author {
    width: 27%;
    position: absolute;
    top: 45px;
    left: 37.5%;
    color: #fff;
    font-size: 14px;
    text-align: center;

    // 溢出父元素隐藏子元素的内容
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    word-break: normal;
    span {
      &::after {
        content: "/";
        margin: 0 4px;
      }
      &:last-of-type {
        //   background: red;
        &::after {
          content: "";
          margin: 0 4px;
        }
      }
    }
  }
  .attention {
    display: inline-block;
    width: 19px;
    height: 14px;
    line-height: 14px;
    background-color: rgb(170, 167, 167);
    border-radius: 9px;
    text-align: center;
    transform: scale(0.83);
    // border: 1px solid gray;
    position: absolute;
    top: 47px;
    right: 33.5%;
    color: #fff;
  }
  .authors {
    font-style: normal;
    display: inline-block;
    width: 19px;
