<template>
  <footer
    class="play"
    :class="{playing: playing}"
  >
    <transition
      name="custom-classes-transition"
      enter-active-class="animate__animated animate__fadeIn"
      leave-active-class="animate__animated animate__fadeOut"
    >
      <!-- @bug  这里有个问题就是当前歌词播放页面如果点击左上角关闭后，再次打开PlayPage时，只显示唱片部分，歌词不见了，当前对应的歌词没有显示，-->
      <!-- 这是为什么呢，这是因为当点击关闭后整个PlayPage页面会销毁， -->
      <keep-alive>
        <PlayPage
          v-if="showPlayPage"
          :currentSong="currentSong"
          :playing="playing"
          :currentTime="currentTime"
          :currentVolume="currentVolume"
          :duration="duration"
          :currentPlayList="currentPlayList"
          :author="author"
          :album="album"
          :playerMode="playerMode"
          :collectionList="collectionList"
          :historyPlayList="historyPlayList"
          :presentPlayList="presentPlayList"
          @change-current-song="$emit('change-current-song',$event)"
          @toggle-show-play-page="showPlayPage = $event"
          @toggle-playing-state="$emit('toggle-playing-state')"
          @toggle-play-model="$emit('toggle-play-model')"
          @previous-song="$emit('previous-song')"
          @next-song="$emit('next-song')"
          @list-cycle="$emit('list-cycle')"
          @song-cycle="$emit('song-cycle')"
          @shuffle-play="$emit('shuffle-play')"
          @current-time-change="$emit('current-time-change',$event)"
          @current-volume-change="$emit('current-volume-change', $event)"
          @delete-the-item="$emit('delete-the-item',$event)"
          @add-history-play-list="$emit('add-history-play-list')"
          @del-all-collection-list="$emit('del-all-collection-list')"
          @del-all-history-list="$emit('del-all-history-list')"
          @del-all-present-list="$emit('del-all-present-list')"
          @add-collection="$emit('add-collection',$event)"
          @cancel-collection="$emit('cancel-collection',$event)"
        >
        </PlayPage>
      </keep-alive>
    </transition>
    <transition
      name="custom-classes-transition"
      enter-active-class="animate__animated animate__slideInUp"
      leave-active-class="animate__animated animate__slideOutDown"
    >
      <!-- @bug 用v-if时，当打开PlayPage的时候，PlayBar会被卸载掉，当它重新打开的时候，用canvas绘制的进度条消失， -->
      <!-- @done 使用v-show不卸载页面 -->
      <PlayBar
        v-show="!showPlayPage"
        :currentSong="currentSong"
        :playing="playing"
        :currentTime="currentTime"
        :duration="duration"
        :author="author"
        :album="album"
        :currentPlayList="currentPlayList"
        @toggle-playing-state="$emit('toggle-playing-state')"
        @change-current-song="$emit('change-current-song',$event)"
        @toggleShowPlayList="toggleShowPlayList"
        @toggle-show-play-page="showPlayPage = $event"
        @change-show-list="showPlayList = $event"
      ></PlayBar>
    </transition>

    <!-- :currentPlayList="currentPlayList" 继续往下传 -->
    <NewPlayList
      v-if="showPlayList"
      :currentPlayList="currentPlayList"
      :currentSong="currentSong"
      :playing="playing"
      :formerPlayList="formerPlayList"
      @change-current-song="$emit('change-current-song',$event)"
      @change-show-list="showPlayList = $event"
      @rank-play-list="$emit('rank-play-list')"
      @del-new-play-list="$emit('del-new-play-list')"
    ></NewPlayList>

  </footer>
</template>

<script>
import PlayBar from '@/components/PlayBar.vue';
import PlayPage from '@/components/PlayPage.vue';
import NewPlayList from '@/components/NewPlayList.vue';


export default {
  data() {
    return {
      showPlayList: false,
      showPlayPage: false,

    }
  },
  components: {
    PlayBar,
    PlayPage,
    NewPlayList
  },
  props: {
    currentSong: Object,
    playing: Boolean,
    duration: Number,
    currentTime: Number,
    currentPlayList: Array,
    playerMode: Number,
    currentVolume: Number,
    formerPlayList: Array,
    collectionList: Array,
    historyPlayList: Array,
    presentPlayList: Array,

  },
  methods: {
    toggleShowPlayList() {
      this.showPlayList = !this.showPlayList;
    },
  },
  computed: {
    // @todo两组不同的数据，选择性渲染列表，根据某个特定属性来决定是否渲染 
    // @done 使用计算属性，判断是否含有currentSong.song，
    author: function () {
      if (this.currentSong.song) {
        return this.currentSong.song.artists;
      } else {
        return this.currentSong.ar;
      }
    },
    album: function () {
      if (this.currentSong.song) {
        return this.currentSong.song.album.name;
      } else {
        return this.currentSong.al.name;
      }
    }
  }
}
</script>

<style lang="less" scoped>
.play {
  width: 100%;
  height: 50px;
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 100;
}
</style>