<template>
  <div>
    <!-- 提示弹层 -->
    <transition name="slide-fade">
    <div
      class="tips"
      v-show="tips.show"
    >
      <h3>{{tips.title}}</h3>
    </div>
    </transition>
  </div>
</template>

<script>
export default {
  data() {
    return {

      // 可以根据实际情况写更复杂的弹层，根据情况扩充tips即可
      tips: {
        show: false,
        title: '',
        timer: null,
      }
    }
  },
  created() {
    this.bus.$on('tips', (data) => {
      this.tips = data;
    })
  },
  watch: {
    // 检测tips变化，显示提示1.5s之后自动关闭，可根据实际情况自动修改时间
    tips: function () {
// @bug 这里有个问题,当频繁点击切换播放状态的时候,你会发现延时器会变快,越点击，跑得越快。
// 这是因为，每点击一次，就切换一个状态，可settimeout就注册一次，所以会越来越快，
// @done 解决的思路就是每次点击的时候进行定时器的清除 clearTimeout(this.timer)，要在data中声明个timer,方便操作
      clearTimeout(this.timer);
      if (this.tips.show) {
          this.timer =  setTimeout(() => {
          this.tips.show = false;
        }, 1500)
      }
    }
  }
}
</script>

<style lang="less" scoped>
.slide-fade-enter-active {
  transition: all .3s ease;
}
.slide-fade-leave-active {
  transition: all .5s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.slide-fade-enter, .slide-fade-leave-to{
  opacity: 0;
}
.tips {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  position: fixed;
  left: 0;
  top: 15rem;
  z-index: 299;
  width: 100%;
  height: 20%;
  color: #fff;
  h3 {
    width: 5.5rem;
    height: 2.2rem;
    padding: 0.2rem 0.3rem;
    font-size: 14px;
    line-height: 30px;
    background-color: rgba(0, 0, 0, 0.7);
    border-radius: 8px;
    text-align: center;
  }
}
</style>