<template>
  <li
    @click="$router.push('/playlist?id=' + item.id)"
    class="card-item"
  >
    <div class="thumb">
      <img
        class="bg"
        :src="`${item.picUrl}?imageView=1&type=webp&thumbnail=247x0`"
      />
      <span>{{ item.playCount | parseCount }}</span>
    </div>
    <p>{{ item.name }}</p>
  </li>
</template>

<script>
export default {
  // @error 1.Missing required prop: "item" 2.TypeError: Cannot read property 'picUrl' of undefined
  // @bug vue项目中，在子组件中通过props传值的时候，在父组件中没有定义的话就会看到类似的报错
  // 这个意思是CardListItem 这个组件中通过props传递一个item属性给父组件，并且item属性是必须的，而在父组件中没有定义这个item，所以要在父组件中定义一下这个item
  props: {
    item: {
      type: Object,
      required: true,
    },
  },
  filters: {
    parseCount: function (value) {
      if (value > 100000000) {
        // @bug 当tofixed(1)保留一位小数的时候，1亿它会在小数点后补个0，显示为1.0亿
        // @done parseFloat()转换字符串成为浮点数,取整显示1亿 
        return parseFloat((value / 100000000).toFixed(1)) + "亿";
      } else if (value > 10000) {
        return parseFloat((value / 10000).toFixed(1)) + "万";
      } else {
        return value;
      }
    }
  }
}
</script>

<style lang="less">
.card-item {
  width: 33%;
  flex-basis: 25%;
  flex-grow: 1;
  padding-bottom: 9px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  &:nth-child(3n + 2) {
    margin: 0 3px;
  }
  .thumb {
    position: relative;
    top: 0;
    left: 0;
    &:after {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 18%;
      background-image: linear-gradient(
        180deg,
        rgba(0, 0, 0, 0.2),
        rgba(0, 0, 0, 0)
      );
    }
    img.bg {
      vertical-align: middle;
      width: 100%;
    }
    span {
      position: absolute;
      top: 3px;
      right: 5px;
      font-size: 10px;
      color: white;
      z-index: 2;
      &::after {
        content: "";
        top: 4px;
        left: -13px;
        background-image: url("data:image/svg+xml;base64,");
        position: absolute;
        width: 11px;
        height: 10px;
      }
    }
  }
  p {
    font-size: 13px;
    margin: 6px;
    line-height: 16px;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}
</style>