<template>
  <div>
    <section class="play-list">
      <div
        class="mask animate__animated animate__fadeIn"
        @click.stop="$emit('change-show-list',false)"
      ></div>
      <div class="points">
        <ol>
          <li :class="{ currentActive : circleIndex == 0 }"></li>
          <li :class="{ currentActive : circleIndex == 1 }"></li>
          <li :class="{ currentActive : circleIndex == 2 }"></li>
        </ol>
      </div>

      <div
        class="swiper animate__animated animate__slideInUp"
        @touchstart="touchStart"
        @touchmove="touchMove"
        @touchend="touchEnd"
      >

        <!-- 歌曲列表轮播图 -->
        <div class="ContinuPlay_box">
          <!-- 收藏列表 -->
          <div
            class="card"
            ref="cardBox"
            @transitionend="animateEnd"
          >
            <h3 class="title">收藏列表<span>({{collectionList.length}})</span></h3>

            <div class="operation">
              <!-- 随机播放 -->
              <div class="random"><svg
                  class="icon"
                  aria-hidden="true"
                >
                  <use xlink:href="#icon-suijibofang"></use>
                </svg>
                <span>随机播放</span>
              </div>

              <!-- 收藏全部 -->
              <div class="collected">
                <svg
                  class="icon"
                  aria-hidden="true"
                >
                  <use xlink:href="#icon-tianjiawenjianjia"></use>
                </svg>
                <span>收藏全部</span>
              </div>

              <!-- 删除 -->
              <div
                class="deletes"
                @click.stop="collectioDeleteAll()"
              ><svg
                  class="icon"
                  aria-hidden="true"
                >
                  <use xlink:href="#icon-shanchu3"></use>
                </svg>
              </div>
            </div>
            <ul class="list">

              <transition-group
                name="list-complete"
                mode="out-in"
              >
                <SongListCard
                  v-for="(item,index) in collectionList "
                  :key="item.id"
                  :item="item"
                  :currentSongId="currentSong.id"
                  :playing="playing"
                  @change-current-song="$emit('change-current-song',$event)"
                  @delete-the-item="type = 3,$emit('delete-the-item',{item,index,type})"
                >{{index + 1}}</SongListCard>
              </transition-group>
            </ul>
          </div>

          <!-- 历史列表 -->
          <div class="card">
            <h3 class="title">历史列表<span>({{historyPlayList.length}})</span></h3>

            <div class="operation">
              <!-- 随机播放 -->
              <div class="random"><svg
                  class="icon"
                  aria-hidden="true"
                >
                  <use xlink:href="#icon-suijibofang"></use>
                </svg>
                <span>随机播放</span>
              </div>

              <!-- 收藏全部 -->
              <div class="collected">
                <svg
                  class="icon"
                  aria-hidden="true"
                >
                  <use xlink:href="#icon-tianjiawenjianjia"></use>
                </svg>
                <span>收藏全部</span>
              </div>

              <!-- 删除 -->
              <div
                class="deletes"
                @click.stop="historyDeleteAll()"
              ><svg
                  class="icon"
                  aria-hidden="true"
                >
                  <use xlink:href="#icon-shanchu3"></use>
                </svg>
              </div>
            </div>
            <ul class="list">

              <!-- 历史列表 -->
              <transition-group
                name="list-complete"
                mode="out-in"
              >
                <SongListCard
                  v-for="(item,index) in historyPlayList "
                  :key="item.id"
                  :item="item"
                  :currentSongId="currentSong.id"
                  :playing="playing"
                  @change-current-song="$emit('change-current-song',$event)"
                  @delete-the-item="type = 2,$emit('delete-the-item',{item,index,type})"
                  @add-history-play-list="$emit('add-history-play-list')"
                >{{index + 1}}</SongListCard>
              </transition-group>
            </ul>
          </div>

          <!-- 当前列表 -->
          <div class="card">
            <h3 class="title">当前列表<span>({{presentPlayList.length}})</span></h3>

            <div class="operation">
              <!-- 随机播放 -->
              <div class="random"><svg
                  class="icon"
                  aria-hidden="true"
                >
                  <use xlink:href="#icon-suijibofang"></use>
                </svg>
                <span>随机播放</span>
              </div>

              <!-- 收藏全部 -->
              <div class="collected">
                <svg
                  class="icon"
                  aria-hidden="true"
                >
                  <use xlink:href="#icon-tianjiawenjianjia"></use>
                </svg>
                <span>收藏全部</span>
              </div>

              <!-- 删除 -->
              <div
                class="deletes"
                @click.stop="currentDeleteAll()"
              ><svg
                  class="icon"
                  aria-hidden="true"
                >
                  <use xlink:href="#icon-shanchu3"></use>
                </svg>
              </div>
            </div>
            <ul class="list">
              <transition-group
                name="list-complete"
                mode="out-in"
              >
                <SongListCard
                  v-for="(item,index) in presentPlayList "
                  :key="item.id"
                  :item="item"
                  :currentSongId="currentSong.id"
                  :playing="playing"
                  @change-current-song="$emit('change-current-song',$event)"
                  @delete-the-item="type = 1,$emit('delete-the-item',{item,index,type})"
                >{{index + 1}}</SongListCard>
              </transition-group>
            </ul>
          </div>
        </div>
      </div>

      <!-- delete页面 -->
      <div
        class="inner-mask"
        v-if="isDelete"
      ></div>
      <div
        class="inner animate__animated animate__slideInUp"
        v-if="isDelete"
      >
        <h3 class="inner-title">删除</h3>
        <p>确定要清空当前播放列表？</p>
        <div class="select">
          <span
            class="left"
            @click="isDelete = false"
          >取消</span>
          <span
            class="right"
            @click="isDelete = false, AllListsDElete()"
          >清空</span>
        </div>
      </div>

      <!-- 提示页面 -->
      <div
        class="inner-mask"
        v-if="tips"
      ></div>
      <div
        class="inner animate__animated animate__slideInUp"
        v-if="tips"
      >
        <h3 class="inner-title">提示</h3>
        <p>当前列表没有更多歌曲了</p>
        <div class="select">
          <span
            class="interpose"
            @click="tips = false"
          >确定</span>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import SongListCard from '@/components/SongListCard.vue'
export default {
  data: function () {
    return {
      circleIndex: 2,

      flag: true, //节流阀

      startX: 0, //触摸开始点的坐标

      endX: 0, //触摸抬起点的位置

      moveX: 0, //移动的距离

      cardWidth: 0, //列表宽度

      cardIndex: 2, //列表下标

      // leftX: 0, // 列表的左偏移量

      // middleX: 0, //中间值

      move: true, //判断点击还是滑动

      // 显示删除提示
      isDelete: false,

      tips: false,

      // 要删除的列表
      currentDelList: 0,
      type: 0,

    }
  },
  components: {
    SongListCard,
  },
  props: {
    showPlayList: Boolean,
    currentPlayList: Array,
    currentSong: Object,
    playing: Boolean,
    collectionList: Array,
    historyPlayList: Array,
    presentPlayList: Array,
  },
  methods: {
    animateEnd() {   // 过渡结束事件
      if (this.cardIndex > 2) { // 判断当前列表索引是否为最后一
        this.cardIndex = 2;
        this.circleIndex = 2;

        this.$refs.cardBox.style.transition = 'none';
        this.$refs.cardBox.style.marginLeft = -this.marginLeftX + "px";

      }
      if (this.cardIndex < 0) {   // 判断当前列表索引是否为第一个列表
        this.cardIndex = 0;
        this.circleIndex = 0;

        this.$refs.cardBox.style.transition = 'none';
        this.$refs.cardBox.style.marginLeft = this.marginLeftX + "px";

      }
    },
    // 触摸开始事件
    touchStart(event) {
      this.move = false;
      this.startX = event.targetTouches[0].clientX;
      // console.log("元素左偏移量", this.leftX);
      // console.log("开始触摸", this.startX);
      // console.log("1.开始时moveX", this.moveX);
      // console.log("");
    },

    // 触摸移动事件
    touchMove(event) {
      this.move = true;
      this.endX = event.targetTouches[0].clientX;
      this.moveX = this.endX - this.startX;
      this.cardWidth = this.$refs.cardBox.offsetWidth;
      this.$refs.cardBox.style.transition = "none";
      this.$refs.cardBox.style.marginLeft = this.marginLeftX + this.moveX + "px";
    },
    touchEnd() {
      // 只有move为true的时候才触发移动事件
      if (this.move) {
        if (this.moveX > 10) {  // 移动距离大于10列表索引--
          this.cardIndex--;
          this.circleIndex--;
          if (this.cardIndex < 0) {
            this.cardIndex = 0;
            this.circleIndex = 0;
            this.$refs.cardBox.style.transition = "all 0.4s ease";
            this.$refs.cardBox.style.marginLeft = this.marginLeftX + "px";
          } else {
            this.$refs.cardBox.style.transition = "all 0.4s ease";
            this.$refs.cardBox.style.marginLeft = this.marginLeftX + "px";
          }

        } else if (this.moveX < -10) { // 移动距离小于10列表索引++
          this.cardIndex++;
          this.circleIndex++;
          if (this.cardIndex > 2) {
            this.cardIndex = 2;
            this.circleIndex = 2;
            this.$refs.cardBox.style.transition = "all 0.4s ease";
            this.$refs.cardBox.style.marginLeft = this.marginLeftX + "px";
          } else {
            this.$refs.cardBox.style.transition = "all 0.4s ease";
            this.$refs.cardBox.style.marginLeft = this.marginLeftX + "px";
          }

        }

      }
      // console.log("3.结束时moveX", this.moveX);
      // console.log("当前列表下标", this.cardIndex);
      // console.log("居中的值", this.middleX);
      // console.log("视口宽度", document.documentElement.clientWidth);
      // console.log("开始触摸", this.startX);
      // console.log("触摸结束", this.endX);
      // console.log("移动距离", this.moveX);
      // console.log("元素当前宽度", this.cardWidth);
      // console.log("");
    },


    // 历史列表垃圾桶的删除
    historyDeleteAll() {
      this.currentDelList = 2;
      this.showDeletePage(this.historyPlayList);
    },

    // 当前播放列表垃圾桶的删除
    currentDeleteAll() {
      this.currentDelList = 1;
      this.showDeletePage(this.presentPlayList);
    },

    // 收藏列表垃圾桶的删除
    collectioDeleteAll() {
      this.currentDelList = 3;
      this.showDeletePage(this.collectionList);


    },

    // 清除按钮
    AllListsDElete() {
      this.deleteLists()
    },

    showDeletePage(list) {
      if (list.length === 0) {
        this.tips = true;
      } else {
        this.isDelete = true;
      }
    },
    deleteLists() {
      switch (this.currentDelList) {
        case 1:
          this.$emit('del-all-present-list');
          console.log("删除当前播放", this.currentDelList);

          break;
        case 2:
          this.$emit('del-all-history-list');
          console.log("删除历史列表", this.currentDelList);
          break;
        case 3:
          this.$emit('del-all-collection-list');
          console.log("删除收藏列表", this.currentDelList);

          break;
      }
    },
  },
  watch: {
    circleIndex() {  // 监视原点索引变化
      if (this.circleIndex >= 2) {
        this.circleIndex = 2;
      }
      if (this.circleIndex <= 0) {
        this.circleIndex = 0;
      }
    }
  },
  computed: {
    marginLeftX() {
      return -(this.boxWidth * this.circleIndex);
    }
  },
  mounted() {
    this.cardWidth = this.$refs.cardBox.offsetWidth;
    this.boxWidth = document.documentElement.clientWidth * 0.896;
    this.$refs.cardBox.style.marginLeft = this.marginLeftX + "px";
  }
}
</script>

<style lang="less" scoped>
.list-complete-enter {
  opacity: 0;
  // transform: translateY(10px);
  // margin-top: 20px;
  margin-right: 22px;
}
.list-complete-leave-to {
  opacity: 0;
}
.list-complete-leave-active {
  position: absolute;
}
.play-list {
  width: 100vw;
  height: 100vh;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 30;
  .mask {
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    position: absolute;
    left: 0;
    top: 0;
  }
  .points {
    width: 100%;
    position: absolute;
    left: 0;
    top: 16.1rem;
    transition: all 1.5s;
    ol {
      width: 14%;
      list-style: none;
      margin: 0 auto;
      display: flex;
      justify-content: space-between;
      align-items: center;
      li {
        width: 0.55rem;
        height: 0.55rem;
        border-radius: 50%;
        background-color: #9e9d9d;
        &.currentActive {
          background-color: #fff;
        }
      }
    }
  }
  .swiper {
    width: 100vw;
    height: 60vh;
    position: absolute;
    left: 0px;
    bottom: 1px;
    overflow: hidden;
    .ContinuPlay_box {
      position: absolute;
      bottom: 0px;
      display: flex;
      padding: 13px 13px;
      .card {
        flex-shrink: 0;
        background-color: white;
        width: 88.1vw;
        height: 55vh;
        border-radius: 25px;
        padding: 15px 10px;
        margin-right: 14px;
        .title {
          width: 100%;
          font-size: 18px;
          padding: 0 10px 2px 10px;
          span {
            font-size: 15px;
            margin-left: 5px;
          }
        }
        .operation {
          display: flex;
          justify-content: space-between;
          align-items: center;
          padding: 10px;
          .icon {
            width: 1.4rem;
            height: 1.4rem;
            fill: #999999;
          }
          .random,
          .deletes,
          .collected {
            display: flex;
            justify-content: space-between;
            align-items: center;
            font-size: 15px;
          }
          .random,
          .collected {
            :nth-child(1) {
              margin-right: 5px;
            }
          }
          .deletes {
            &::before {
              content: "";
              position: relative;
              left: -28px;
              border-left: 0.01rem solid #d3d3d3;
              height: 1rem;
            }
          }
        }
        .list {
          height: 82%;
          overflow-y: auto;
        }
      }
    }
  }
  .inner {
    width: 285px;
    height: 148px;
    background-color: #fff;
    position: absolute;
    left: 45px;
    top: 330px;
    border-radius: 16px;
    z-index: 2;
    display: flex;
    text-align: center;
    flex-direction: column;
    justify-content: space-between;
    .inner-title {
      height: 45px;
      line-height: 3;
      font-size: 20px;
    }
    p {
      height: 53px;
      line-height: 3;
      color: #000;
      padding: 0 15px;
      border-bottom: 1px solid #cdcdcd;
      font-size: 15px;
    }
    .select {
      height: 48px;

      // margin-top: 60px;
      // text-align: right;
      display: flex;
      justify-content: space-around;
      padding: 0 14px;
      span {
        width: 150px;
        height: 100%;
        line-height: 3;

        // margin-left: 80px;
      }
      .left {
        color: rgb(0, 110, 255);
      }
      .right {
        color: #ff3333;
        border-left: 1px solid #cdcdcd;
      }
      .interpose {
        color: rgb(0, 110, 255);
      }
    }
  }
  .inner-mask {
    content: "";
    display: inline-block;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.2);
    position: absolute;
    z-index: 1;
  }
}
</style>